import useLoadAndSelect, { UseLoadAndSelectReturn } from 'store/hooks/useLoadAndSelect'
import { organizations } from 'store/thunks/organizations';
import { shallowEqual } from 'react-redux';
import { MOrganization } from 'store/types/models/id';

function useOrganizationList(searchText: string, delimiter?: string): UseLoadAndSelectReturn<MOrganization[]> {
    const orgList = useLoadAndSelect<MOrganization[]>({
        actionCreator: organizations.all,
        selector: (state) => Object.values(state.id.organizations),
        postParams: [searchText, delimiter],
        equality: shallowEqual,
        deduplicate: ['organizations', delimiter + searchText]
    })
    return orgList
}

export default useOrganizationList;