import useLoadAndSelect from 'store/hooks/useLoadAndSelect';
import {  isEqual } from 'lodash'
import { getOrgInfo } from "store/thunks/organizations";
import { MOrganization } from "store/types/models/id";

function useOrganization( organizationId: string ) {
    const organization = useLoadAndSelect<MOrganization>({
        actionCreator: organizationId ? getOrgInfo : undefined,
        selector(state) {
            return state.id.organizations?.[organizationId]
        },
        equality: isEqual,
        postParams: organizationId,
        deduplicate: ['organization', organizationId, 'public']
    });
    return organization;
}

export default useOrganization;