////// Packages //////////////////
import React, {useCallback, FC} from 'react';
import { Button, Media, Collapse } from 'reactstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import ExperienceForm from './ExperienceForm';

////// Global Components /////////
import Verified from 'components/Verified';
import Avatar from 'components/Avatar';

////// CommonJS Helpers //////////
import { getDuration } from 'helpers/utils'

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { IPhoto } from 'store/types/models/id';

interface Props {
  _id?: string;
  photo?: IPhoto;
  position?: string;
  organization: string;
  dateStart: string;
  dateEnd?: string;
  location?: string;
  divider?: boolean;
  verified?: boolean;
  isEditable?: boolean;
  setFormId: (formId: string)=>void;
  formId: string;
  personId: string;
  to?: string;
}
const ExperienceSummary: FC<Props> = ({ _id, photo, position, organization, dateStart, dateEnd, location, divider=true, verified=false, isEditable, setFormId, formId, personId, to }) => {
  const handleLoadForm = useCallback(() => {
    setFormId(_id)
  }, [_id, setFormId])

  const collapse = _id === formId;

  return (
    <> 
      <Collapse toggler="#togglerAddExperience" isOpen={collapse}>
        <ExperienceForm personId={personId} experienceId={_id} setFormId={setFormId} showDelete={true} />
        <hr className="border-dashed border-bottom-0 my-4" />
      </Collapse>
      <Collapse isOpen={!collapse}>
        <Media>
          <Link to={to}>
            <Avatar src={[photo?.location]} jwt={[photo?.jwt]} name={organization} size="3xl" />
          </Link>
          <Media body className="position-relative pl-3 btn-reveal-trigger">
            <h6 className="fs-0 mb-0 d-flex justify-content-between align-items-start">
              <span>
                {position}
                {verified && <Verified />}
              </span>
              {isEditable && (
                <Button color="link" className="btn-reveal py-0 px-2" onClick={handleLoadForm}>
                  <FontAwesomeIcon icon="pencil-alt" />
                </Button>
              )}
            </h6>
            <p className="mb-1">
              {organization}
            </p>
            <p className="text-1000 mb-0">{getDuration(dateStart, dateEnd)}</p>
            <p className="text-1000 mb-0">{location}</p>
            {divider && <hr className="border-dashed border-bottom-0" />}
          </Media>
        </Media>
      </Collapse>
    </>
  )
};

export default ExperienceSummary;
