////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////
import Education from './components/Education';
import Experience from './components/Experience';
import Contact from './components/Contact';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types ////////////////////
import { MPerson } from 'store/types/models/id';

interface Props {
  person: MPerson;
  isEditable: boolean;
}

const ProfileAside: FC<Props> = ({person, isEditable}) => {
  return (
    <>
      <Contact person={person} className="mb-3" />
      <Experience person={person} className="mb-3" isEditable={isEditable} />
      <Education person={person} className="mb-3" isEditable={isEditable} />
    </>
  );
};

export default ProfileAside;
