////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////

////// Global Components /////////
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia'

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';

interface Props {
    person: MPerson;
    className: string;
}

const DisplayEmails: FC<Props> = ({person, className}) => {
    return (
        <>
            {person?.emails && person.emails.length > 0 &&
                <AsideResponsiveMedia icon="envelope" className={className}>
                    <h6 className="mb-0 font-weight-bold">Email Addresses</h6>
                            {person?.emails?.map( ({description, address}, index) => ( 
                                <h6 key={index} className="mb-0 text-truncate">
                                    <em className="pr-1 pt-1 d-block d-sm-inline pt-sm-0">
                                        {description}:
                                    </em> 
                                    <a className="text-900" href={`mailto:${address}`}>{address} </a> 
                                </h6>
                            ))}
                </AsideResponsiveMedia>
            }
        </>
    );
};



export default DisplayEmails;
