////// Packages //////////////////
import React, { FC, useCallback, useState, useEffect } from 'react';
import DateTime, { DatetimepickerProps } from 'react-datetime';
import moment, {Moment} from 'moment'
////// Local Components //////////
import EditableSection from './EditableSection';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { Thunk } from 'store/store';

interface Props extends DatetimepickerProps {
    date: string | Moment | undefined;
    tag?: string;
    setter: Thunk<any>;
    setterParams?: any | any[];
}

const DateEditSection: FC<Props> = ({date, setter, tag: Tag = 'span', setterParams, ...dateProps }) => {
    const thunk = useAction(setter);
    const [storedDate, setStoredDate] = useState(date);

    const handleDateChange = useCallback((value: string | Moment) => setStoredDate(moment(value).format('MM/DD/YYYY')), []);

    const handleSubmit = async () => {
        const arrParams = Array.isArray(setterParams) ? setterParams : [setterParams]
        const data = await thunk.post(storedDate, ...arrParams)
        return data?.success
    }

    useEffect(() => {
        setStoredDate(date);
    }, [date])

    const {value: _unused, onChange: _unused2, ...dateTimeProps} = dateProps
    return (
        <EditableSection 
            baseContent={<Tag>{date}</Tag>}
            editContent={
                <>
                    <DateTime
                        value={storedDate}
                        onChange={handleDateChange}
                        {...dateTimeProps}
                    />
                    {thunk.error && 
                        <h6>
                            {thunk.error}
                        </h6>
                    }
                </>
            }
            handleSubmit={handleSubmit}
            loading={thunk.loading}
        />
    );
};


export default DateEditSection;