////// Packages //////////////////
import React, { Fragment, useState, FC } from 'react';
import { Card, CardBody, CardProps, Collapse } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames';

////// Local Components //////////
import ExperienceSummary from './ExperienceSummary';
import ExperienceForm from './ExperienceForm';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import Flex from 'components/Flex';

////// CommonJS Helpers //////////
import { isIterableArray } from 'helpers/utils';
import { MPerson } from 'store/types/models/id';

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props extends CardProps {
  person: MPerson;
  isEditable: boolean;
}

const Experience: FC<Props> = ({ person , isEditable=false, ...rest }) => {
  // Data
  const [formId, setFormId] = useState('none')

  return (
    <Card {...rest}>
      <FalconCardHeader title="Experience" light={!isEditable} />
      <CardBody className={classNames('fs--1', { 'bg-light': isEditable })}>
        {isIterableArray(person?.experiences) &&
          person.experiences?.slice(0).sort((a, b) => a.dateStart < b.dateStart ? 1 : -1).map((experience, index, arr) => (
            <ExperienceSummary 
              {...experience} 
              personId={person?._id} 
              setFormId={setFormId} 
              formId={formId} 
              isEditable={isEditable} 
              divider={true} 
              key={index}  
            />
          ))
        }
        {isEditable && (
          <Fragment>
            <Collapse toggler="#togglerAddExperience" isOpen={!formId}>
              <ExperienceForm 
                personId={person?._id}
                experienceId={formId}
                setFormId={setFormId}
               />
              <hr className="border-dashed border-bottom-0 my-4" />
            </Collapse>
            <Collapse isOpen={formId === 'none'}>
              <Flex
                align="center"
                className="mb-4 text-primary cursor-pointer fs-0"
                id="togglerAddExperience"
                onClick={() => {
                  setFormId('');
                }}
              >
                <span className="circle-dashed">
                  <FontAwesomeIcon icon="plus" />
                </span>
                <span className="ml-3">Add new experience</span>
              </Flex>
            </Collapse>
          </Fragment>
        )}
      </CardBody>
    </Card>
  );
};

export default Experience;
