////// Packages //////////////////
import React, { FC, useEffect, useState } from 'react'
import {FormGroup, Row, Col, Label, InputGroupAddon, InputGroup, Input, UncontrolledTooltip } from 'reactstrap'
////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import ButtonIconTooltip from 'components/ButtonIconTooltip'
import ButtonIcon from 'components/ButtonIcon'
import { IAddress } from 'store/types/models/id';
////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    id: string;
    address?: IAddress
}

const AddressForm: FC<Props> = ({id, address}) => {
    const [title, setTitle] = useState('')
    const [street, setStreet] = useState([''])
    const [city, setCity] = useState('')
    const [state, setState] = useState('')
    const [zipCode, setZipCode] = useState('')
    
    useEffect(()=>setTitle(address?.title || ''), [address?.title])
    useEffect(()=>setStreet(address?.street || []), [address?.street])
    useEffect(()=>setTitle(address?.city || ''), [address?.city])
    useEffect(()=>setTitle(address?.state || ''), [address?.state])
    useEffect(()=>setTitle(address?.zipCode || ''), [address?.zipCode])
    

    return (
        <FormGroup className="btn-reveal-trigger">
            <Row noGutters>
                <Col>
                    <FormGroupInput
                        id={`address-title-${id}`}
                        label='Title'
                        placeholder="Title..."
                        value={title}
                        onChange={e => setTitle(e.target.value)}
                    />
                    <FormGroupInput
                        id={`address-street-${id}`}
                        label='Street'
                        placeholder="Street..."
                        value={street}
                        onChange={e => setStreet(e.target.value)}
                    />
                    <FormGroupInput
                        id={`address-city-${id}`}
                        label='City'
                        placeholder="City..."
                        value={city}
                        onChange={e => setCity(e.target.value)}
                    />
                    <FormGroupInput
                        id={`address-state-${id}`}
                        label='State'
                        placeholder="State..."
                        value={state}
                        onChange={e => setState(e.target.value)}
                    />
                    <FormGroupInput
                        id={`address-zipcode-${id}`}
                        label='Zip Code'
                        placeholder="Zip Code..."
                        value={zipCode}
                        onChange={e => setZipCode(e.target.value)}
                    />
                </Col>
                <Col>
                    <h6>Street Address</h6>
                    {street.map( (line, i) => (
                        <FormGroup key={i}>
                            <Label>{'Line ' + (i + 1)}</Label>
                            <InputGroup>
                                <Input
                                    id={`address-line-${i}-${id}`}
                                    label={'Line ' + (i + 1)}
                                    placeholder="..."
                                    value={line}
                                    onChange={e => 
                                        setStreet( street => {
                                            const newStreet = [...street]
                                            newStreet[i] = e.target.value
                                            return newStreet
                                        }
                                    )}
                                />
                                <InputGroupAddon addonType="append">
                                    <ButtonIconTooltip 
                                        id={`delete-${id}-${i}`}
                                        icon="trash" 
                                        color="danger" 
                                        onClick={()=>setStreet(street => street.filter( (_, indx) => indx !== i))} 
                                    >
                                        Delete Line
                                    </ButtonIconTooltip>
                                </InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                    ))}
                    <ButtonIcon 
                        size="sm" 
                        icon="plus" 
                        color="falcon-primary" 
                        onChange={()=>setStreet(street => [...street, ''])}
                    >
                        Add Line
                    </ButtonIcon>
                </Col>
            </Row>
            <Row>
                <Col>
                </Col>
                <Col>
                </Col>
            </Row>
        </FormGroup>
    )
}

export default AddressForm