////// Packages //////////////////
import React, {FC} from 'react';
import { CardBody, CardHeader, Card, Button, Row, Col } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import dayjs from 'dayjs'

////// Local Components //////////

////// Global Components /////////
import Avatar from 'components/Avatar';
import LoadingGateway from 'components/LoadingGateway';
import ProgressiveImage from 'components/ProgressiveImage';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useOrganization from 'store/hooks/organization/useOrganization'
import StarRating from 'components/StarRating';


////// Thunks ////////////////////


interface Props {
    setOpenModal: (openModal: boolean)=>void;
    organizationId: string;
    isEditable: boolean;
}

const OrganizationBanner: FC<Props> = ({setOpenModal, organizationId, isEditable}) => {
    const {value: organization, ...orgLoader} = useOrganization(organizationId);

    return (
        <Card className="mb-3">
            <LoadingGateway dataLoader={orgLoader} >
                <CardHeader className={`position-relative min-vh-25 mb-7`}>
                    <div className="bg-holder overflow-hidden rounded-soft rounded-bottom-0 bg-dark position-relative">
                        <ProgressiveImage src={organization?.coverPhoto?.location || ''} width="100%" height={250} size='l' style={{ objectFit: 'cover' }} />
                    </div>
                    <Avatar
                        src={organization?.avatar?.location}
                        size="5xl"
                        rounded="circle"
                        className=" avatar-profile"
                        mediaClass="img-thumbnail shadow-sm"
                    />
                </CardHeader>
                <CardBody>
                    <Row>
                        <Col sm={9} md={6}>
                            <h4 className="mb-3 btn-reveal-trigger">
                                {organization?.name}
                            </h4>
                            <h5 
                                className="fs-0 font-weight-normal ml-2"
                                style={ {textIndent: "-0.5rem"} } 
                            >
                                <i>{organization?.tagline}</i>
                            </h5>
                            <h5 className="fs-0 font-weight-normal ml-2 mt-3" style={ {textIndent: "-0.5rem"}} >
                                <FontAwesomeIcon icon="landmark" className="mr-3"/>

                                {organization?.foundingDate ?
                                    'Founded ' + dayjs(organization?.foundingDate).format('MMMM D, YYYY')
                                :
                                    'Founding unknown'
                                }
                            </h5>
                            <h5 className="fs-0 font-weight-normal ml-2 mt-3" style={ {textIndent: "-0.5rem"}} >
                                Rating:
                                <StarRating rating={organization?.rating} className=" ml-3 d-inline-block" />
                            </h5>
                        </Col>
                        <Col 
                            xs="auto" 
                            className="ml-sm-auto mt-4 mt-sm-0 d-flex align-items-end "
                            
                        >
                            {isEditable && 
                                <Button color="primary" size="sm" onClick={() => setOpenModal(true)}>
                                    <FontAwesomeIcon icon="pencil-alt" className="mr-2" />
                                        Edit Organization 
                                </Button>
                            }
                        </Col>
                    </Row>
                </CardBody>
            </LoadingGateway>
        </Card>
    );
};


OrganizationBanner.propTypes = {

};


export default OrganizationBanner;
