import React, { FC } from 'react';
import ButtonIcon, { Props as ButtonIconProps } from './ButtonIcon';
import { UncontrolledTooltip } from 'reactstrap';


export interface Props extends ButtonIconProps {
  id: string ;
  placement?: 'top' | 'bottom' | 'left' | 'right';
  children: React.ReactNode;
}

const ButtonIconTooltip: FC<Props> = ({ id, placement='bottom', children, size='sm', color='falcon-default', ...rest }) => (
  <>
    <ButtonIcon id={id} size={size} color={color} {...rest} />
    <UncontrolledTooltip placement={placement} target={id}>
      {children}
    </UncontrolledTooltip>
  </>
);


export default ButtonIconTooltip;
