////// Packages //////////////////
import React, { FC, useCallback, useState } from 'react'
import { Col, Row, FormGroup, InputGroup, Input, Alert, UncontrolledTooltip, Button } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';
import EditableSection from 'compositions/modal/EditableSection';
import AddressForm from 'compositions/AddressForm';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendOrgAddresses } from 'store/thunks/organizations';

////// Types /////////////////////
import { IAddress, MOrganization } from 'store/types/models/id';

interface Props {
    addresses: IAddress[];
    organizationId: MOrganization['_id'];
}

const ModalMailingAddress: FC<Props> = ({addresses, organizationId}) => {
    const setMailingAddresses = useAction( sendOrgAddresses )

    const handleSubmitOrgAddress = async () => {
        const data = await setMailingAddresses.post(addresses, organizationId)
        return data?.success
    }

    const handleMailingAddressAdd = useCallback( () => ( p => [...p, {title: '', street: '', city: '', state: '', zipCode: ''}] ) , []);
    
    return (
        <ModalMediaContent title="Mailing Address" icon="envelope">
            <EditableSection
                baseContent={
                    addresses?.map(({ _id, title, street, city, state, zipCode }) => (
                        <div key={_id}>
                            <strong>
                                <em>
                                    {title}: 
                                </em>
                            </strong>
                            {street.map( addrLine => <>{addrLine}<br /></>)} 
                            {`${city}, ${state} ${zipCode}`}
                        </div>
                    ))
                }
                baseId="mailing-address"
                editContent={
                    <>
                        {addresses?.map((address, index) => (
                            <>
                                <AddressForm key={index} id={_id} address={address}/>
                            </>
                        ))}
                        <Row>
                            {setMailingAddresses.error && <Alert color="danger">{setMailingAddresses.error}</Alert>}
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handleMailingAddressAdd} id="add-mailing-address">
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                                <UncontrolledTooltip placement="bottom" target="add-mailing-address">
                                    Add mailing address
                                </UncontrolledTooltip>
                            </Col>
                        </Row>
                    </>
                    
                }
                handleSubmit={handleSubmitOrgAddress}
                loading={setMailingAddresses.loading}
                />
        </ModalMediaContent>
    )
}

export default ModalMailingAddress