import { organizations as organizationsDuck } from '../ducks/id';
import { BaseDuck } from "store/ducks/BaseDuck";
import { MOrganization, IPhone, IEmail, IAddress } from "store/types/models/id";
import uploadDoc from "helpers/uploadDoc";
import { ActionCreator } from "store/types/interface/common";

export interface OrganizationController extends BaseDuck<MOrganization> {
    all: ActionCreator<['getOrgList']>;
    getAdminInfo: ActionCreator<['getAdminOrgInfo']>;
}

export const organizations: OrganizationController = organizationsDuck as OrganizationController

export const all: ActionCreator<['getOrgList']> = (searchText: string, delimiter?: string, numberPerPage = 100) => {
    const actions = [{
        name: 'getOrgList',
        data: {
            searchText,
            delimiter,
            numberPerPage
        }
    }] as const;
    return { actions }
}
organizations.all = all;

export const getAdminInfo: ActionCreator<['getAdminOrgInfo']> = (organizationId: string) => {
    const actions = [{
        name: 'getAdminOrgInfo',
        data: {organizationId}
    }] as const;
    return { actions }
}

export const getPrivateOrgInfo: ActionCreator<['getPrivateOrgInfo']> = (organizationId: string) => {
    const actions = [{
        name: 'getPrivateOrgInfo',
        data: {organizationId}
    }] as const;
    return {actions}
}

export const getOrgInfo: ActionCreator<['getPublicOrgInfo']> = (organizationId: string) => {
    console.log('running getOrgInfo');
    const actions = [{
        name: 'getPublicOrgInfo',
        data: {organizationId}
    }] as const;
    return { actions }
}

export const sendOrganizationAvatar: ActionCreator<['editOrgInfo']> = async ( file: Blob,  organizationId: string, description = '', metaData = {}) => {
    const newFile = await uploadDoc(file, 'avatar');
    if (!newFile?.success || !newFile.files?.avatar) return {cancel: true};

    const actions = [{
        name: 'editOrgInfo',
        data: {
            _id: organizationId,
            avatar: {
                location: newFile.files?.avatar, 
                description,
                metaData
            }
        }
    }] as const;
    return {actions}
}

export const sendOrganizationCover: ActionCreator<['editOrgInfo']> = async ( file: Blob,  organizationId: string, description = '', metaData = {}) => {

    const newFile = await uploadDoc(file, 'cover');
    if (!newFile?.success || !newFile.files?.cover) return { cancel: true };

    const actions = [{
        name: 'editOrgInfo',
        data: {
            _id: organizationId,
            coverPhoto: {
                location: newFile.files.cover, 
                description,
                metaData
            }
        }
    }] as const;
    return {actions}
}

