import React, { useRef, useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  Col,
  Media,
  Row
} from 'reactstrap';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import BootstrapTable, { PaginationOptions, BootstrapTableProps } from 'react-bootstrap-table-next';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';

////// Local Components //////////
import AddOrganizationModal from './AddOrganizationModal';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import ButtonIcon from 'components/ButtonIcon';
import Flex from 'components/Flex';
import Avatar from 'components/Avatar';

////// CommonJS Helpers //////////
import { getPaginationArray } from 'helpers/utils';
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { MOrganization, MPerson } from 'store/types/models/id';
import useOrganizationList from 'store/hooks/organization/useOrganizationList';
import StarRating from 'components/StarRating';
import ToolkitProvider from 'react-bootstrap-table2-toolkit';
import PaginatingTableCard from 'compositions/table/PaginatingTableCard';
import useTypingTimer from 'hooks/useTypingTimer';

interface BootstrapPropsPartial extends Partial<BootstrapTableProps> { }
interface PaginationChildProps extends PaginationOptions {
  tableId?: string;
  bootstrap4?: boolean;
}

interface FixedFactoryProps {
  paginationProps: PaginationChildProps;
  paginationTableProps: BootstrapPropsPartial;
}

// import customers from '../../data/e-commerce/customers';
type NameFormatterProps = Partial<MOrganization>;

const nameFormatter = (_dataField: string, { _id, avatar, name }: NameFormatterProps) => {
  const displayName = name
  return (
    <Link to={`/social/organizations/${_id}`}>
      <Media tag={Flex} align="center">
        <Avatar src={avatar?.location} name={displayName} />
        <Media body className="ml-2">
          <h5 className="mb-0 fs--1">{displayName}</h5>
        </Media>
      </Media>
    </Link>
  );
};

const websiteFormatter = (website: string) => website ? <a href={`${/http/.test(website) ? website : `http://${website}`}`} target="_blank" rel="noreferrer">{website}</a> : <></>;
// const phoneFormatter = (phone: string) => <a href={`tel:${phone}`}>{phone}</a>;

// const phoneFormatter = (phone: string) => <a href={`tel:${phone}`}>{phone}</a>;

const phoneFormatter = (number?: string) => number ? <a href={`tel:${number}`}>{number}</a> : <></>;

const emailFormatter = (email?: string) => email ? <a href={`mailto:${email}`}>{email}</a> : <></>;

const ContactOrganizations = () => {
    const [openModal, setOpenModal] = useState(false);

    const timer = useTypingTimer(750)
    const [searchText, setSearchText] = useState('');
    const { value: organizations, ...organizationsList } = useOrganizationList( searchText );

    const csvExportRef = useRef(undefined as {onExport: ()=>void} | undefined);

    const orgDisplayInfo = organizations?.map( org => ({
        ...org,
        topWebsite: org.websites?.[0],
        topPhoneNumber: org.phones?.[0]?.number,
        topEmail: org.emails?.[0]?.address
    }))
    const columns = [
        {
            dataField: 'name',
            text: 'Name',
            headerClasses: 'border-0',
            classes: 'border-0 py-2 align-middle',
            formatter: nameFormatter,
            sort: true,
            search: true
        },
        {
            dataField: 'topWebsite',
            headerClasses: 'border-0 d-none d-md-table-cell',
            text: 'Website',
            classes: 'border-0 py-2 align-middle d-none d-md-table-cell',
            formatter: websiteFormatter,
            sort: true,
            search: true
        },
        {
            dataField: 'topPhoneNumber',
            headerClasses: 'border-0 d-none d-lg-table-cell',
            text: 'Phone',
            classes: 'border-0 py-2 align-middle d-none d-lg-table-cell',
            formatter: phoneFormatter,
            sort: true
        },
        {
            dataField: 'topEmail',
            headerClasses: 'border-0 d-none d-lg-table-cell',
            text: 'Email',
            classes: 'border-0 py-2 align-middle d-none d-lg-table-cell',
            formatter: emailFormatter,
            sort: true,
            search: true
        },
        {
            dataField: 'rating',
            headerClasses: 'border-0',
            text: 'Rating',
            classes: 'border-0 py-2 align-middle',
            formatter: (rating: number) => <StarRating rating={rating} />, // eslint-disable-line react/display-name
            sort: true
        }
        ];

    return (
        <>
            <AddOrganizationModal isOpen={openModal} setIsOpen={setOpenModal} />
            <PaginatingTableCard
                title="Organizations"
                cardClass="mb-3"
                paginationOptions={{
                    sizePerPage: 12
                }}
                csvExportRef={csvExportRef}
                exportCSV={{fileName: "Happin Organizations Export " + dayjs().format("MM-DD-YYYY") + '.csv'}}
                data={orgDisplayInfo || []}
                columns={columns}
                handleNew={()=>setOpenModal(true)}
                onSearchText={text=>timer(()=>setSearchText(text))}
                search
            />
        </>
    );
}


export default ContactOrganizations;