////// Packages //////////////////
import React, { FC, useState, useCallback } from 'react';
import { useParams } from 'react-router';
import { useSelector } from 'react-redux';

////// Local Components //////////
import OrganizationContent from './OrganizationContent';
import OrganizationBanner from './OrganizationBanner';
import OrganizationAside from './OrganizationAside';
import OrganizationFooter from './OrganizationFooter';
import OrgConfigurationModal from './OrgConfigurationModal';

////// Global Components /////////
import ContentWithAsideLayout from 'layouts/ContentWithAsideLayout';
import useOrganization from 'store/hooks/organization/useOrganization';
import LoadingGateway from 'components/LoadingGateway';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {

}

const Organization: FC<Props> = () => {
    const { orgId } = useParams<{orgId: string}>();
    const [openModal, setOpenModal] = useState(false);

    const organizationId = useSelector(state => state.app.login.organizationId!);
    const {value: organization, ...orgLoader} = useOrganization(orgId);

    const toggleModal = useCallback( () => setOpenModal(!openModal) , [setOpenModal, openModal] );
    const isEditable = organization?.recordOwner === organizationId
    return (
        <LoadingGateway dataLoader={orgLoader}>
            <ContentWithAsideLayout
                banner={<OrganizationBanner organizationId={orgId || organizationId} setOpenModal={setOpenModal} isEditable={isEditable} />}
                content={<OrganizationContent organizationId={orgId || organizationId} setOpenModal={setOpenModal} isEditable={isEditable} />}
                aside={<OrganizationAside organizationId={orgId || organizationId} />}
                footer={<OrganizationFooter organizationId={orgId || organizationId}  />}
                isStickyAside={ false }
            />
            <OrgConfigurationModal organizationId={orgId || organizationId} isOpen={openModal} toggle={toggleModal} />
        </LoadingGateway>
    );
};

export default Organization;
