////// Packages //////////////////
import React, {FC} from 'react'
import { Card, CardBody } from "reactstrap";
import { Link } from 'react-router-dom';

////// Local Components //////////

////// Global Components /////////
import FalconCardHeader from "components/FalconCardHeader";
import Notification from "compositions/Notification";


////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';

const activities  = []

interface Props {
  items?: number;
  person: MPerson;
}

const ActivityLog: FC<Props> = ({ items = 5, person }) => {
  const activityLog = activities.slice(0, items);

  return (
    <Card className="mb-3">
      <FalconCardHeader title="Activity log">
        <Link to="/pages/activity" className="text-sans-serif">
          All logs
        </Link>
      </FalconCardHeader>

      <CardBody className="fs--1 p-0">
        {activityLog.map((activity, index) => {
          const roundedClass = activityLog.length === index + 1 ? 'rounded-top-0' : 'rounded-0';
          return (
            <Notification
              {...activity}
              key={index}
              className={`border-x-0 border-bottom-0 border-300 ${roundedClass}`}
            />
          );
        })}
      </CardBody>
    </Card>
  );
};


export default ActivityLog;