////// Packages //////////////////
import React, { useState, FC, useEffect } from 'react';
import { Button, Card, CardBody, CardFooter, Form, Row, Col, UncontrolledTooltip } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import ExpandableParagraph from 'components/ExpandableParagraph'
import FormGroupInput from 'components/FormGroupInput';
import Flex from 'components/Flex';
import {MPerson} from 'store/types/models/id';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { sendPersonBio } from 'store/thunks/people';
import useAction from 'store/hooks/useAction';
import { toast } from 'react-toastify';

interface Props {
  person: MPerson;
  isEditable: boolean;
}

const ProfileIntro: FC<Props> = ({ person, isEditable }) => {
  const [collapsed, setCollapsed] = useState(true);
  const [editMode, setEditMode] = useState(false);
  const [bio, setBio] = useState(person?.bio || '');
  const setBioAction = useAction(sendPersonBio);

  const handleCollapse = () => setCollapsed(!collapsed)

  useEffect(() => {
    if (person?.bio) setBio(person.bio)
  }, [person?.bio])

  const handleSubmit = async () => {
    const data = await setBioAction.post(bio, person?._id)
    if (data?.success) {
      toast.success('Changed bio')
      setEditMode(false);
    } else {
      toast.error(data?.error || 'Something went wrong')
    }
  };

  return (
    <Card className="mb-3">
      <FalconCardHeader title="Bio" className="btn-reveal-trigger">
        {isEditable && (
          <>
            <UncontrolledTooltip target="Tooltip-EditBio" placement="top">
              Edit Bio
            </UncontrolledTooltip> 
            <Button id="Tooltip-EditBio" color="link" className="btn-reveal py-0 px-2" onClick={() => setEditMode(!editMode)}>
              <FontAwesomeIcon icon="pencil-alt" />
            </Button>
          </>
        )}
      </FalconCardHeader>
      { editMode ? (
        <CardBody>
          <Form>
            <Row>
              <Col xs={12}>
                <FormGroupInput
                  id="bio"
                  value={bio}
                  onChange={({ target }) => setBio(target.value)}
                  type="textarea"
                  rows="13"
                  label="Bio"
                />
                <Col tag={Flex} xs={12} justify="end">
                  <Button color="primary" onClick={handleSubmit}>
                    Update
                  </Button>
                </Col>
              </Col>
            </Row>
          </Form>
        </CardBody>

      )
        :
        (
          <>
            <CardBody className="text-justify">
              <ExpandableParagraph text={person?.bio} collapsed={collapsed} baseChars={400} />
            </CardBody>

            <CardFooter className="bg-light p-0 border-top">
              <Button color="falcon-primary" block to="/pages/events" onClick={handleCollapse}>
                Show {collapsed ? 'more' : 'less'}
                <FontAwesomeIcon icon="chevron-up" className="ml-1 fs--2" transform={collapsed ? 'rotate-180' : ''} />
              </Button>
            </CardFooter>
          </>
        )
      }
    </Card >
  );
};

export default ProfileIntro;
