////// Packages //////////////////
import React, { useCallback, useState, FC } from 'react';
import { Button, Col, Row, Spinner, UncontrolledTooltip } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames'

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
    baseId: string;
    baseContent: React.ReactNode; 
    editContent: React.ReactNode; 
    handleSubmit: ()=>Promise<boolean | undefined | null>; 
    loading?: boolean;
}
const EditableSection: FC<Props> = ({baseId, baseContent, editContent, handleSubmit, loading}) => {
    const [editMode, setEditMode] = useState(false);

    const handleEditModeChange = useCallback(() => setEditMode(!editMode), [setEditMode, editMode]);
    const handleSubmitClick = useCallback(async () => {
        const success = await handleSubmit();
        setEditMode(!success);
    }, [handleSubmit, setEditMode])

    return (
        <Row className="btn-reveal-trigger">
            <Col>
                {editMode ? editContent : baseContent}
            </Col>
            <Col xs={12} lg="auto" className="ml-lg-auto">
                    <Button 
                        id={`${baseId}-submit`} 
                        color="link" 
                        onClick={handleSubmitClick} 
                        className={classNames("mr-2 btn-reveal py-0 px-2 text-success", {"d-none": !editMode})}
                    >
                        <FontAwesomeIcon icon="check" />
                    </Button>
                    <UncontrolledTooltip placement="bottom" target={`${baseId}-submit`}>
                        Submit
                    </UncontrolledTooltip>
                    <Button 
                        id={`${baseId}-cancel`} 
                        color="link" 
                        onClick={handleEditModeChange} 
                        className={classNames("ml-2 btn-reveal py-0 px-2 text-secondary", {"d-none": !editMode})}
                    >
                        <FontAwesomeIcon icon="times" />
                    </Button>
                    <UncontrolledTooltip placement="bottom" target={`${baseId}-cancel`}>
                        Cancel
                    </UncontrolledTooltip>
                    <Button 
                        id={`${baseId}-edit`} 
                        color="link" 
                        onClick={handleEditModeChange} 
                        className={classNames("ml-2 btn-reveal py-0 px-1", {"d-none": editMode})}
                    >
                        <FontAwesomeIcon icon="pencil-alt" />
                        {loading && <Spinner color="light" size="sm" />}
                    </Button>
                    <UncontrolledTooltip placement="bottom" target={`${baseId}-edit`}>
                        Edit
                    </UncontrolledTooltip>
            </Col>
        </Row>
    );
};


export default EditableSection;
