import React, { FC } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import Profile from 'layouts/social/Profile/Profile';
import { RouteComponentProps } from 'react-router'
import UnderConstruction from 'layouts/errors/UnderConstruction';
import ContactPeople from 'layouts/social/Contacts/ContactPeople';
import Organization from 'layouts/administration/organization/Organization';
import ContactOrganizations from 'layouts/social/Contacts/ContactOrganizations';

interface Props extends RouteComponentProps {

}

const SocialRoutes: FC<Props> = ({ match: { url } }) => (
  <Switch>
    <Route path={`${url}/profile/:personId`} component={Profile} />
    <Route path={`${url}/people`} exact component={ContactPeople} />
    <Route path={`${url}/organizations/:orgId`} exact component={Organization} />
    <Route path={`${url}/organizations`} exact component={ContactOrganizations} />
    <Route path={`${url}/relationships`} exact component={UnderConstruction} />
    <Route path={`${url}/chat`} exact component={UnderConstruction} /> 
    
    {/*Redirect*/}
    <Redirect to="/errors/404" />
  </Switch>
);

export default SocialRoutes;
