////// Packages //////////////////
import React, { useEffect, useCallback, FC } from 'react';
import { InputProps } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props extends InputProps {
    value: string;
    onChange: (e: React.ChangeEvent<HTMLInputElement>)=>void;
    pattern?: string;
}

const PhoneNumberInput: FC<Props> = ({id, label, value, pattern = '(###) ### - ####', ...rest}) => {
    const {onChange, ...inputProps} = rest;

    const handleNumberChange = useCallback( (e) => {
        const numbers = e?.target?.value?.match(/[0-9]/g) || '';
        let indx = 0;
        const formattedNumber = pattern.split('').map( (v, i) => {
            if (indx < numbers.length) {
                let digit;
                if (v === '#') {
                    digit = numbers[indx] || v;
                    indx++;
                } else {
                    digit = v;
                }
                return digit;
            } else {
                return null;
            }
        }).join('')
        e.target.value = formattedNumber;
        return onChange(e);
    }, [pattern, onChange])

    useEffect(() => {
        const e = {target: {value}}
        handleNumberChange(e)
    }, [value]);

    return (
        <FormGroupInput 
            {...inputProps}
            id={id}
            label={label}
            value={value}
            valid={value.length === pattern.length}
            invalid={value.length !== pattern.length}
            onChange={handleNumberChange}
            type="tel"
        />
    )
}

export default PhoneNumberInput;