import React, { Fragment, FC } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { UncontrolledTooltip } from 'reactstrap';
import uniqueId from 'lodash/uniqueId';

interface Props {
  placement?: "top" | "auto-start" | "auto" | "auto-end" | "right" | "bottom" | "left";
  id?: string;
}

const Verified: FC<Props> = ({ placement="top", id }) => {
  const verifiedUniqueId = id || uniqueId('verified_');

  return (
    <Fragment>
      <FontAwesomeIcon
        icon="check-circle"
        transform="shrink-4 down-2"
        id={verifiedUniqueId}
        className="text-primary ml-1"
      />
      <UncontrolledTooltip placement={placement} target={verifiedUniqueId}>
        Verified
      </UncontrolledTooltip>
    </Fragment>
  );
};


export default Verified;
