////// Packages //////////////////
import React, {FC} from 'react';
import Media from 'reactstrap/es/Media';
import { Link } from 'react-router-dom';

////// Local Components //////////

////// Global Components /////////
import Avatar from 'components/Avatar';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
  name: string;
  avatarSrc?: string;
  role: string;
  personId: string;
}

const ActiveUser: FC<Props> = ({ name, avatarSrc, role, personId }) => (
  <Media className="align-items-center mb-3">
    <Avatar src={avatarSrc} name={name} />
    <Media body className="ml-3">
      <h6 className="mb-0 font-weight-semi-bold">
        <Link className="text-900" to={ `/social/profile/${personId}` } >
          {name}
        </Link>
      </h6>
      <p className="text-500 fs--2 mb-0">{role}</p>
    </Media>
  </Media>
);


export default ActiveUser;