import React, { useState, useEffect, FC } from 'react';

interface Props {
  text: string | undefined;
  collapsed: boolean;
  baseChars: number;
}

const ExpandableParagraph: FC<Props> = ({text='', collapsed=false, baseChars=400}) => {
    const [showChars, setShowChars] = useState(400);

    const collapseSpeed = 30;
  
    useEffect(() => {
      let collapse: number;
      let timer: NodeJS.Timeout;
      if (collapsed) {
        collapse = -1 * collapseSpeed;
        timer = setTimeout(() => {
          if (showChars > baseChars) {
            setShowChars(showChars + collapse)
          }
        }, 10)
      } else {
        collapse = collapseSpeed;
        timer = setTimeout(() => {
          if (showChars < text.length) {
            setShowChars(showChars + collapse)
          }
        }, 10)
      }
      return (() => clearTimeout(timer))
    }, [collapsed, showChars])

    return (
        <span style={{whiteSpace: 'pre-wrap'}}>
            { (text.slice(0, showChars) + ((text.length > showChars) ? '...' : '') ) }
        </span>
    );
};

export default ExpandableParagraph;
