////// Packages //////////////////
import React, { useCallback, FC, useState } from 'react';
import { Card, Button, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import LoadingGateway from 'components/LoadingGateway';
import ActiveUser from 'compositions/ActiveUser';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useOrganization from "store/hooks/organization/useOrganization";
import useOrgEmployees from "store/hooks/person/useOrgEmployees";
import makeName from 'helpers/makeName';
import AddPersonModal from 'layouts/social/Contacts/AddPersonModal';

////// Thunks ////////////////////

interface Props {
    setOpenModal: (openModal: boolean)=>void;
    organizationId: string;
    isEditable?: boolean;
}

const OrganizationContent: FC<Props> = ({ setOpenModal, organizationId, isEditable }) => {
    const {value: organization, ...organizationLoader} = useOrganization(organizationId);
    const {value: employees, ...employeesLoader} = useOrgEmployees(organizationId);
    const [isOpen, setIsOpen] = useState(false);

    return (
        <>
            <Card className="mb-3">
                    <FalconCardHeader title="About" className="btn-reveal-trigger" />
                    <CardBody >
                        <LoadingGateway dataLoader={organizationLoader}>
                            {organization?.bio?.split("\n").map( (str, i) => <p key={i}>{str}</p>)}
                        </LoadingGateway>
                    </CardBody>
                </Card>
                <Card className="mb-3">
                    <FalconCardHeader title="Employees" className="btn-reveal-trigger">
                        {isEditable && 
                            <>
                                <Button color="link" className="btn=reveal py-0 px-2" onClick={()=>setIsOpen(true)} >
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                                <AddPersonModal organizationId={organizationId} isOpen={isOpen} setIsOpen={setIsOpen} />
                            </>
                        }
                    </FalconCardHeader>
                    <CardBody >
                        <LoadingGateway dataLoader={employeesLoader}>
                            {employees?.map(({ _id, name, position, avatar }) => (
                                <ActiveUser
                                    name={name ? makeName(name, {first: true, common: true, last: true}) : 'No Name'}
                                    avatarSrc={avatar?.location}
                                    role={position}
                                    personId={_id}
                                    key={_id}
                                />
                            ))}
                        </LoadingGateway>
                    </CardBody>
            </Card>
        </>
    );
};


OrganizationContent.propTypes = {

};


export default OrganizationContent;
