////// Packages //////////////////
import React, { FC } from 'react'
import { useDispatch } from 'react-redux';

////// Local Components //////////

////// Global Components /////////
import CreatablePhotoCard from 'compositions/CreatablePhotoCard';

////// CommonJS Helpers //////////
import base64toBlob from 'helpers/base64toBlob';

////// Custom Hooks //////////////
import usePersonPhotos from 'store/hooks/person/usePersonPhotos'
////// Thunks ////////////////////
import { addPersonPhotos } from 'store/thunks/people';
import { MPerson } from 'store/types/models/id';
import { DropzoneFile } from 'store/store';
import useAction from 'store/hooks/useAction';

interface Props {
  className: string;
  person: MPerson;
}

const ProfilePhotos: FC<Props> = ({ className, person }) => {
  const {post} = useAction(addPersonPhotos);
  const {value: personPhotos} = usePersonPhotos(person?._id);

  const handleImageSubmit = async (photos: DropzoneFile[]) => {
    const data = await post(
        photos.map(
          image => ({
            data: base64toBlob( image.base64 ), 
            name: image.path
          })
        ), 
        person._id
      )
    let closeModal = false
    if (data?.success) {
      closeModal = true;
    }
    return closeModal;
  }

  return (
    <CreatablePhotoCard photos={personPhotos || []} onSubmitPhotos={handleImageSubmit} className={className} />
  )
}

export default ProfilePhotos;
