////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////

////// Global Components /////////
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia';
import { MOrganization } from 'store/types/models/id';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
    organization: MOrganization;
    className: string;
}

const DisplayWebsites: FC<Props> = ({organization, className}) => {
    return (
        <>
            {organization?.websites && organization.websites.length > 0 &&
                <AsideResponsiveMedia icon="globe-americas" className={className}>
                    <h6 className="mb-0 font-weight-bold">Websites</h6>
                    {organization?.websites?.map( (website, index) => {
                        const pattern = /\/\//
                        const websiteLink = pattern.test(website) ? website : 'http://' + website
                        return (
                            <h6 key={index} className="mb-0 p-1 p-sm-0">
                                <a className="text-900" href={websiteLink} target="_blank" rel='noreferrer' key={index}>
                                    {website} 
                                </a>
                            </h6>
                        )
                    })}
                </AsideResponsiveMedia>
            }
        </>
    );
};


export default DisplayWebsites;