import React, {FC} from 'react';
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia';
import { MPerson } from 'store/types/models/id';

interface Props {
    person: MPerson;
    className: string;
}

const DisplayPhones: FC<Props> = ({person, className}) => {
    return (
        <>
            {person?.phones && person.phones.length > 0 && 
                <AsideResponsiveMedia icon="phone" className={className}>
                    <h6 className="mb-0 font-weight-bold">Phone Numbers</h6>
                    {person?.phones?.map( ({description, number}, index) => ( 
                        <h6 key={index} className="mb-0 pt-1 pt-sm-0">
                            <em className="pr-1 d-block d-sm-inline">
                                {description}:
                            </em> 
                            {number} 
                        </h6>
                    ))}
                </AsideResponsiveMedia>
            }
        </>
    );
};



export default DisplayPhones;
