////// Packages //////////////////
import React, { useCallback, useEffect, useState, FC } from 'react';
import {Button, Col, Row, Alert, Input, Label, FormGroup, InputGroup, InputGroupAddon, UncontrolledTooltip} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';
import EditableSection from 'compositions/modal/EditableSection';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import {sendPersonPhones} from 'store/thunks/people';

////// Types ///////////////////
import { IPhone } from 'store/types/models/id';
import PhoneNumberInput from 'components/PhoneNumberInput';



interface Props {
    phones?: IPhone[];
    personId: string;
}

const ModalPhones: FC<Props> = ({phones, personId}) => {
    const sendPhones = useAction(sendPersonPhones);
    const [newPhones, setNewPhones] = useState(phones || []);

    useEffect(() => {setNewPhones(phones || [])}, [phones])


    /* const handleDescriptionChange = useCallback(index => e => {
        setNewPhones( newPhones.map( (v, i) => i === index ? {description: e.target.value, number: v.number} : v ))
    }, [newPhones]) */

    const handlePhoneAdd = useCallback( () => setNewPhones( p => [...p, {description: '', number: ''}] ) , []);
    
    const handleSubmit = useCallback(async () => {
        const data = await sendPhones.post(newPhones.map(v=>({...v, description: v.description})), personId);
        if (data?.success) {
            return true;
        } else {
            return false;
        }
    }, [newPhones, personId]);

    return (
        <ModalMediaContent title="Phone" icon="phone">
            <EditableSection
                baseId="phone"
                baseContent={
                    phones?.map(({ _id, description, number }) => (
                        <div key={_id}>
                            <strong><em>{description}: </em></strong> {number}
                        </div>
                    ))
                }
                editContent={
                    <>
                        {newPhones?.map(({ description, number }, index) => (
                            <>
                            <FormGroup className="btn-reveal-trigger" key={index}>
                                <Row noGutters>
                                    <Col>
                                        <InputGroup> 
                                            <InputGroupAddon addonType="prepend">
                                                <Button 
                                                    color="link" 
                                                    className="text-danger py-0 px-2" 
                                                    onClick={() => setNewPhones( newPhones.filter( (_, i) => i !== index) ) }
                                                    id={`phone-${index}-profile`}
                                                >
                                                    <FontAwesomeIcon icon="trash" />
                                                </Button>
                                                <UncontrolledTooltip placement="bottom" target={`phone-${index}-profile`}>
                                                    Delete Phone
                                                </UncontrolledTooltip>
                                            </InputGroupAddon>
                                            
                                            <Input
                                                id={`phone-description-${index}`}
                                                label=''
                                                placeholder="Phone description.."
                                                value={description}
                                                onChange={e => setNewPhones(newPhones.map( (v, i) => (
                                                        i === index 
                                                            ? {...v, description: e.target.value} 
                                                            : v 
                                                        )
                                                ))}
                                            />
                                        
                                        </InputGroup>
                                    </Col>
                                    <Col>
                                        <PhoneNumberInput 
                                            id={`phone-number-${index}`}
                                            label=''
                                            placeholder="(###) ###-####"
                                            value={number}
                                            onChange={e => setNewPhones(newPhones.map( (v, i) => (
                                                i === index 
                                                    ? {...v, number: e.target.value} 
                                                    : v 
                                                )
                                            ))}
                                        />
                                    </Col>
                                </Row>
                            </FormGroup>
                            </>
                        ))}
                        <Row>
                            {sendPhones.error && <Alert color="danger">{sendPhones.error}</Alert>}
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handlePhoneAdd} id="add-phone-now">
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                                <UncontrolledTooltip placement="bottom" target="add-phone-now">
                                    Add phone
                                </UncontrolledTooltip>
                            </Col>
                        </Row>
                    </>

                }
                handleSubmit={handleSubmit}
                />
        </ModalMediaContent>
    )
    
};


export default ModalPhones;
