////// Packages //////////////////
import React, { FC, useState } from 'react'
import { Button, Col, FormGroup, Input, InputGroup, Label, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap'


////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import FormGroupSelect from 'components/FormGroupSelect';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useOrganizationList from 'store/hooks/organization/useOrganizationList';

////// Thunks ////////////////////

////// Types /////////////////////
import { IName, MOrganization } from 'store/types/models/id';
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';
import useAction from 'store/hooks/useAction';
import { createPersonContact } from 'store/thunks/people';
import { toast } from 'react-toastify';
import { useHistory } from 'react-router';
import { IPhone, IEmail } from 'store/types/models/id'
import PhoneNumberInput from 'components/PhoneNumberInput';

interface Props {
    isOpen: boolean,
    setIsOpen: React.Dispatch<React.SetStateAction<boolean>>
    organizationId?: MOrganization['_id']
    shouldRedirect?: boolean;
}

const DEFAULT_PHONE = {description: '', number: ''}
const DEFAULT_EMAIL = {description: '', address: ''}

const AddPersonModal: FC<Props> = ({isOpen, setIsOpen, organizationId, shouldRedirect}) => {
    const history = useHistory()
    const [name, setName] = useState({} as IName);
    const [position, setPosition] = useState('');
    const [organization, setOrganization] = useState<{value: string, label: string} | undefined>(undefined);
    const [location, setLocation] = useState('');
    const [birthday, setBirthday] = useState('');
    const [bio, setBio] = useState('');
    const [phone, setPhone] = useState<IPhone>(DEFAULT_PHONE);
    const [email, setEmail] = useState<IEmail>(DEFAULT_EMAIL);
    const [orgSearchText, setOrgSearchText] = useState(undefined as string | undefined);

    const {value: organizations, ...organizationsLoader} = useOrganizationList(orgSearchText || '')
    const organizationOptions = organizations?.map(org => ({value: org._id, label: org.name}))
    const modalOrganization = organizationId ? organizations?.find( org => org._id === organizationId ) : undefined;

    const createPersonAction = useAction( createPersonContact )

    const handleCreatePerson = async () => {
        if (!modalOrganization?._id && !organization?.value) {
            toast.error('Organization selection is invalid')
            return
        }
        const data = await createPersonAction.post({name, position, organizationId: modalOrganization?._id || organization.value, location, birthday, bio, phone, email})
        if (data?.success) {
            toast.success('Created ' + name.first + ' ' + name.last)
            setName({} as IName);
            setPosition('');
            setOrganization(undefined);
            setLocation('');
            setBirthday('');
            setBio('');
            setPhone(DEFAULT_PHONE);
            setEmail(DEFAULT_EMAIL);
            setOrgSearchText('');
            setIsOpen(false);
            const newPersonId = data?.records?.[0]?.people?.[0]?._id;
            if (newPersonId && shouldRedirect) {
                history.push('/social/profile/' + newPersonId)
            }
        } else {
            toast.error(data?.error || "something went wrong")
        }
    }

    return (
        <Modal size='lg' isOpen={isOpen} toggle={()=>setIsOpen(o=>!o)}>
            <ModalHeader className='bg-light border-bottom-0'>
                Create Contact
            </ModalHeader>
            <ModalBody>
                <Row>
                    <Col lg>
                        <h6>Name</h6>
                        <div className="pl-3 border border-top-0 border-bottom-0 border-right-0">
                            <FormGroupInput
                                id="new-person-name-prefix"
                                label="Prefix"
                                placeholder="Prefix"
                                value={name.prefix}
                                onChange={e=>setName(name=>({...name, prefix: e.target.value}))}
                            />
                            <FormGroupInput
                                id="new-person-name-first"
                                label="First"
                                placeholder="First"
                                value={name.first}
                                onChange={e=>setName(name=>({...name, first: e.target.value}))}
                            />
                            <FormGroupInput
                                id="new-person-name-common"
                                label="Common"
                                placeholder="Common"
                                value={name.common}
                                onChange={e=>setName(name=>({...name, common: e.target.value}))}
                            />
                            <FormGroupInput
                                id="new-person-name-middle"
                                label="Middle"
                                placeholder="Middle"
                                value={name.middle}
                                onChange={e=>setName(name=>({...name, middle: e.target.value}))}
                            />
                            <FormGroupInput
                                id="new-person-name-last"
                                label="Last"
                                placeholder="Last"
                                value={name.last}
                                onChange={e=>setName(name=>({...name, last: e.target.value}))}
                            />
                            <FormGroupInput
                                id="new-person-name-title"
                                label="Title"
                                placeholder="Title"
                                value={name.title}
                                onChange={e=>setName(name=>({...name, title: e.target.value}))}
                            />
                        </div>
                    </Col>
                    <Col lg>
                        <h6>Other Info</h6>
                        <div className="pl-3 border border-top-0 border-bottom-0 border-right-0">
                            {organizationId 
                                ? 
                                    <>
                                        <hr />
                                        <h6>Member of {modalOrganization?.name}</h6> 
                                        <hr />
                                    </>
                                : <FormGroupSelect
                                        id="select-organization"
                                        label="Select Organization"
                                        inputValue={orgSearchText}
                                        onInputChange={text=>setOrgSearchText(text)}
                                        options={organizationOptions}
                                        value={organization}
                                        onChange={option=>setOrganization(option)}
                                        isLoading={organizationsLoader.loading}
                                    />
                            }
                            <FormGroupInput
                                id="new-person-position"
                                label="Position"
                                placeholder="Position"
                                value={position}
                                onChange={e=>setPosition(e.target.value)}
                            />
                            <FormGroupInput
                                id="new-person-location"
                                label="Location"
                                placeholder="i.e. 'Main Office', 'San Francisco, CA'"
                                value={location}
                                onChange={e=>setLocation(e.target.value)}
                            />
                            <FormGroup>
                                <Label for="new-person-phone">Phone</Label>
                                <InputGroup>
                                    <Input 
                                        id="new-person-phone-type"
                                        placeholder="Type"
                                        size={10}
                                        value={phone.description}
                                        onChange={e=>setPhone(p=>({...p, description: e.target.value}))}
                                    />
                                    <PhoneNumberInput 
                                        id="new-person-phone"
                                        placeholder="Phone"
                                        value={phone.number}
                                        onChange={e=>setPhone(p=>({...p, number: e.target.value}))}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <Label for="new-person-email">Email</Label>
                                <InputGroup>
                                    <Input 
                                        id="new-person-email-type"
                                        placeholder="Type"
                                        size={10}
                                        value={email.description}
                                        onChange={e=>setEmail(p=>({...p, description: e.target.value}))}
                                    />
                                    <Input
                                        id="new-person-email"
                                        placeholder="Email"
                                        value={email.address}
                                        onChange={e=>setEmail(p=>({...p, address: e.target.value}))}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <Label>Birthday</Label>
                                <DateTimeInputGroup
                                    id="new-person-birthday"
                                    value={birthday}
                                    onChange={date=>setBirthday(date)}
                                    timeFormat={false}
                                    dateFormat="MM/DD/YYYY"
                                    pickerProps={{
                                        closeOnSelect: true
                                    }}
                                    
                                />
                            </FormGroup>
                        </div>
                    </Col>
                </Row>
                
                <FormGroupInput
                    type="textarea"
                    rows={4}
                    id="new-person-bio"
                    label="About"
                    placeholder="About"
                    value={bio}
                    onChange={e=>setBio(e.target.value)}
                />
            </ModalBody>
            <ModalFooter className='bg-light border-top-0'>
                <Button size='sm' color='primary' onClick={handleCreatePerson}>
                    Submit
                </Button>
            </ModalFooter>
        </Modal>
        
    )
}

export default AddPersonModal;