////// Packages //////////////////
import React, { useState, useEffect, FC } from 'react';
import { Alert, Button, Col, FormGroup, Label, Row, UncontrolledTooltip } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import usePerson from 'store/hooks/person/usePerson';
import useOrganization from 'store/hooks/organization/useOrganization';

////// Thunks ////////////////////
import { sendPersonLocation, sendPersonPosition, sendPersonName } from 'store/thunks/people'
import useAction from 'store/hooks/useAction';
import BounceSpinner from 'components/BounceSpinner';


interface Props {
  personId: string;
  isEditable?: boolean;
  children: React.ReactNode;
}

const ProfileBannerIntro: FC<Props> = ({ personId, isEditable, children }) => {
  const thunkLocation = useAction( sendPersonLocation );
  const thunkPosition = useAction( sendPersonPosition );
  const thunkName = useAction( sendPersonName );

  const {value: person} = usePerson(personId);
  const {value: organization} = useOrganization(person?.organizationId || '');
  
  const name = person?.name;
  const formattedName = makeName(name, {first: true, common: true, last: true})
  const headline = `${person?.position || 'Employee'} at ${organization?.name || ' company'}`

  const [editMode, setEditMode] = useState(false);

  const [namePrefix, setNamePrefix] = useState(name?.prefix || '');
  const [nameFirst, setNameFirst] = useState(name?.first || '');
  const [nameCommon, setNameCommon] = useState(name?.common || '');
  const [nameMiddle, setNameMiddle] = useState(name?.middle || '');
  const [nameLast, setNameLast] = useState(name?.last || '');
  const [nameTitle, setNameTitle] = useState(name?.title || '');

  const [position, setPosition] = useState(person?.position || '');
  const [location, setLocation] = useState(person?.location || '');

  useEffect(() => {
    if (name) {
      setNamePrefix(name.prefix || '')
      setNameFirst(name.first)
      setNameCommon(name.common || '')
      setNameMiddle(name.middle || '')
      setNameLast(name.last)
      setNameTitle(name.title || '')
    }
  }, [JSON.stringify(name)])

  const handleSubmit = async () => {
    const nameObj = {
      prefix: namePrefix, 
      first: nameFirst, 
      common: nameCommon, 
      middle: nameMiddle,
      last: nameLast,
      title: nameTitle
    }
    await thunkName.post(nameObj, person?._id)
    await thunkPosition.post(position, person?._id)
    const data = await thunkLocation.post(location, person?._id) 
    if (data?.success) {
      setEditMode(false);
    }
  }

  return (
    <Col lg="auto" className="btn-reveal-trigger">
      {editMode ?
        <div className="border-right pr-3">
          <FormGroup>
            <h6>Name</h6>
          </FormGroup>
            <FormGroupInput
              id='prefix'
              label='Prefix'
              value={namePrefix}
              onChange={e => setNamePrefix(e.target.value)}
              bsSize="sm"
              type="text"
              name="prefix-name"
              placeholder="Enter prefix.."
            />
          <FormGroupInput
            id='first'
            label='First*'
            invalid={!nameFirst}
            feedback={ !nameFirst && 'First name is required' }
            value={nameFirst}
            onChange={e => setNameFirst(e.target.value)}
            bsSize="sm"
            type="text"
            name="first-name"
          />
          <FormGroupInput
            id='nickname'
            label='Nickname'
            value={nameCommon}
            onChange={e => setNameCommon(e.target.value)}
            bsSize="sm"
            type="text"
            name="common-name"
          />
          <FormGroupInput
            id='middle'
            label='Middle'
            value={nameMiddle}
            onChange={e => setNameMiddle(e.target.value)}
            bsSize="sm"
            type="text"
            name="middle-name"
          />
          <FormGroupInput
            id='last'
            label={`Last*`}
            invalid={!nameLast}
            feedback={ !nameFirst && 'Last name is required' }
            value={nameLast}
            onChange={e => setNameLast(e.target.value)}
            bsSize="sm"
            type="text"
            name="last-name"
          />
          <FormGroupInput
            id='title'
            label='Title (suffix)'
            value={nameTitle}
            onChange={e => setNameTitle(e.target.value)}
            bsSize="sm"
            type="text"
            name="suffix-name"
          />
          <FormGroupInput
            id='position'
            label='Position*'
            invalid={!position}
            feedback={ !position && 'Position is required' }
            value={position}
            onChange={e => setPosition(e.target.value)}
            size={35}
            bsSize="sm"
            type="text"
            name="position"
            placeholder="Position at organization.."
          />
          <FormGroupInput
            id='location'
            label='Location'
            value={location}
            onChange={e => setLocation(e.target.value)}
            bsSize="sm"
            type="text"
            name="city"
            placeholder="City, State"
          />
          {(thunkName.error || thunkPosition.error || thunkLocation.error) && 
            <Row>
              <Col>
                <Alert color="danger" className="fs--1">{thunkName.error + ' ' + thunkPosition.error + ' ' + thunkLocation.error}</Alert>
              </Col>
            </Row>
          }
          <Row>
            <Col className="ml-auto" xs="auto">
              {(thunkName.loading || thunkPosition.loading || thunkLocation.loading) && <BounceSpinner color="info" size="sm"  className="mx-1"/>}
              <Button color="primary" onClick={handleSubmit} disabled={ !nameFirst || !nameLast || !position }>
                Update
              </Button>
            </Col>
          </Row>
        </div>
        :
        <>
          
          <h4 className="mb-1 ">
            {formattedName}
            {isEditable && (
              <>
                <UncontrolledTooltip target="Tooltip-PersonalInfo" placement="top" >
                    Edit Personal Info
                </UncontrolledTooltip>
                <Button color="falcon-primary" id="Tooltip-PersonalInfo" className="py-0 px-2 ml-2" onClick={() => setEditMode(!editMode)}>
                  <FontAwesomeIcon icon="pencil-alt" />
                </Button>
              </>
            )}
          </h4>
          {headline && <h5 className="fs-0 font-weight-normal">{headline}</h5>}
          {location && <p className="text-500">{location}</p>}
        </>
      }
      {children}
      <hr className="border-dashed my-4 d-lg-none" />
    </Col>
  );
};


export default ProfileBannerIntro;
