////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getPaginationArray } from 'helpers/utils';
import React, { FC } from 'react'
import BootstrapTable, { BootstrapTableProps, PaginationOptions } from 'react-bootstrap-table-next';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { CSVProps } from 'react-bootstrap-table2-toolkit';
import { Button, Card, CardBody, Col, Row } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface BootstrapPropsPartial extends Partial<BootstrapTableProps> { }
interface PaginationChildProps extends PaginationOptions {
    tableId?: string;
    bootstrap4?: boolean;
}
interface FixedFactoryProps {
    paginationProps: PaginationChildProps;
    paginationTableProps: BootstrapPropsPartial;
}


export interface PaginatingTableProps extends Omit<BootstrapTableProps, 'keyField'> {
    paginationOptions: PaginationOptions,
    csvExportRef?: React.MutableRefObject<{ onExport(): void } | undefined>,
    exportCSV?: boolean | CSVProps;
    keyField?: string;
    onSelectRow?: (row: any)=>void;
}

const DefaultNoData = () => (
    <Card>
        <CardBody>
            No Data Available
        </CardBody>
    </Card>
)


const PaginatingTable: FC<PaginatingTableProps> = ({
    paginationOptions,
    csvExportRef,
    data,
    columns,
    classes = "table-dashboard table-striped table-sm fs--1 border-bottom border-200 mb-0 table-dashboard-th-nowrap",
    rowClasses = "btn-reveal-trigger border-top border-200",
    headerClasses = "bg-200 text-900 border-y border-200",
    bordered = false,
    exportCSV,
    keyField = "_id",
    hover=true,
    noDataIndication=DefaultNoData,
    tabIndexCell=true,
    defaultSortDirection="asc",
    rowStyle,
    onSelectRow,
    ...otherTableProps
}) => {

    const handleNextPage = ({ page, onPageChange }: PaginationChildProps) => () => {
        onPageChange && onPageChange(page! + 1, paginationOptions.sizePerPage!);
    };

    const handlePrevPage = ({ page, onPageChange }: PaginationChildProps) => () => {
        onPageChange && onPageChange(page! - 1, paginationOptions.sizePerPage!);
    };

    return (

        <PaginationProvider pagination={paginationFactory(paginationOptions)}>
            {({ paginationProps, paginationTableProps }: FixedFactoryProps) => {
                const lastIndex = paginationProps.page! * paginationProps.sizePerPage!;
                return (
                    <>
                        <div className="table-responsive">
                            <ToolkitProvider
                                keyField="_id"
                                data={data}
                                columns={columns}
                                bootstrap4
                                exportCSV={exportCSV}
                            >
                                {
                                    ({ baseProps, csvProps }) => {
                                        if (csvExportRef) csvExportRef.current = csvProps
                                        return (
                                            <BootstrapTable
                                                bordered={bordered}
                                                classes={classes}
                                                rowClasses={rowClasses}
                                                headerClasses={headerClasses}
                                                hover={hover}
                                                noDataIndication={noDataIndication}
                                                tabIndexCell={tabIndexCell}
                                                defaultSortDirection={defaultSortDirection}
                                                rowStyle={{cursor: 'pointer', ...rowStyle}}
                                                selectRow={onSelectRow && {
                                                    mode: 'radio',
                                                    clickToSelect: true,
                                                    hideSelectColumn: true,
                                                    classes: 'bg-soft-primary',
                                                    onSelect: onSelectRow
                                                }}
                                                {...otherTableProps}
                                                {...baseProps}
                                                {...paginationTableProps}
                                            />
                                        )
                                    }
                                }
                            </ToolkitProvider>
                        </div>
                        <Row noGutters className="px-1 py-3 flex-center">
                            <Col xs="auto">
                                <Button
                                    color="falcon-default"
                                    size="sm"
                                    onClick={handlePrevPage(paginationProps)}
                                    disabled={paginationProps.page === 1}
                                >
                                    <FontAwesomeIcon icon="chevron-left" />
                                </Button>
                                {getPaginationArray(paginationProps.totalSize!, paginationProps.sizePerPage!).map(pageNo => (
                                    <Button
                                        color={paginationProps.page === pageNo ? 'falcon-primary' : 'falcon-default'}
                                        size="sm"
                                        className="ml-2"
                                        onClick={() => paginationProps.onPageChange!(pageNo, paginationProps.sizePerPage!)}
                                        key={pageNo}
                                    >
                                        {pageNo}
                                    </Button>
                                ))}
                                <Button
                                    color="falcon-default"
                                    size="sm"
                                    className="ml-2"
                                    onClick={handleNextPage(paginationProps)}
                                    disabled={lastIndex >= paginationProps.totalSize!}
                                >
                                    <FontAwesomeIcon icon="chevron-right" />
                                </Button>
                            </Col>
                        </Row>
                    </>
                );
            }}
        </PaginationProvider>
    )
}

export default PaginatingTable
