////// Packages //////////////////
import React, { FC, useEffect, useState } from 'react'
import { Card, CardBody, Form } from 'reactstrap'

////// Local Components //////////
import PaginatingTable, {PaginatingTableProps} from './PaginatingTable'

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader'
import ButtonIcon from 'components/ButtonIcon'
import SearchBox from 'compositions/SearchBox'
import andRegexFromString from 'helpers/andRegexFromString'
import { ColumnDescription } from 'react-bootstrap-table-next'
////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////


// eslint-disable-next-line
export interface CustomSearchColumn<T extends object=any, U=any> extends ColumnDescription<T, U> {
    search?: boolean | ( (data: any) => string )
}

interface Props extends PaginatingTableProps {
    title: string;
    cardClass?: string;
    handleNew?: () => void;
    handleFilter?: () => void;
    handleExport?: (() => void) | false;
    filterContent?: React.ReactNode;
    componentSize?: "sm" | "md" | "lg";
    columns: CustomSearchColumn[];
    onSearchText?: (text: string)=>void
}

const PaginatingTableCard: FC<Props> = ({ 
    title, 
    cardClass, 
    handleNew, 
    handleFilter, 
    handleExport, 
    filterContent, 
    csvExportRef, 
    search, 
    componentSize="sm",
    data, 
    columns, 
    paginationOptions,
    onSearchText,
    ...rest
}) => {
    const defaultExport = csvExportRef && ( () => {console.log(csvExportRef.current) ; csvExportRef.current?.onExport() } )
    
    const selectedExport = handleExport === false ? undefined : (handleExport || defaultExport)

    const [searchText, setSearchText] = useState('');

    useEffect(()=>onSearchText && onSearchText(searchText), [searchText])

    const searchPattern = andRegexFromString(searchText);

    const filteredData = searchText 
                            ? data.filter( d => columns
                                                .map( c => {
                                                    if (c.search) {
                                                        if (c.search === true) {
                                                            const fields = c.dataField.split('.')
                                                            let searchData = d
                                                            for (const f of fields) {
                                                                searchData = typeof searchData === 'object' ? searchData[f] : undefined
                                                            }
                                                            if (searchData) return searchData
                                                        } else {
                                                            return c.search(d)
                                                        }
                                                    }
                                                })
                                                .filter(s=>s)
                                                .join(' ')
                                                .match(searchPattern)
                                )
                            : data


    return (
        <Card className={cardClass}>
            <FalconCardHeader title={title} light={false}>
                <Form inline>
                    { search && <SearchBox bsSize={componentSize === "md" ? undefined: componentSize} value={searchText} onChange={e=>setSearchText(e.target.value)}/> }
                    {handleNew && 
                        <ButtonIcon
                            icon="plus"
                            transform="shrink-3 down-2"
                            color="falcon-default"
                            size={componentSize}
                            className="ml-2"
                            onClick={handleNew}
                        >
                            New
                        </ButtonIcon>
                    }
                    {handleFilter && 
                        <ButtonIcon
                            icon="filter"
                            transform="shrink-3 down-2"
                            color="falcon-default"
                            size={componentSize}
                            className="ml-2"
                            onClick={handleFilter}
                        >
                            Filter
                        </ButtonIcon>
                    }
                    {selectedExport && 
                        <ButtonIcon
                            icon="external-link-alt"
                            transform="shrink-3 down-2"
                            color="falcon-default"
                            size={componentSize}
                            className="ml-2"
                            onClick={selectedExport}
                        >
                            Export
                        </ButtonIcon>
                    }
                </Form>
                
            </FalconCardHeader>
            <CardBody className="p-0">
                { filterContent }
                <PaginatingTable
                    csvExportRef={csvExportRef}
                    columns={columns}
                    data={filteredData}
                    paginationOptions={{
                        ...paginationOptions,
                        custom: true,
                        totalSize: filteredData.length || 1
                    }}
                    {...rest}
                />
            </CardBody>
        </Card>
    )
}

export default PaginatingTableCard