////// Packages //////////////////
import React, { useState, useEffect, useCallback, FC } from 'react';
import { Alert, Button, Col, CustomInput, Form, FormGroup, Row, Label } from 'reactstrap';
import { useSelector} from 'react-redux';
import {isEqual} from 'lodash';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

////// Local Components //////////
import AsideFormInput from 'compositions/AsideFormInput';
import AsideFormDate from 'compositions/AsideFormDate';

////// Global Components /////////
import FalconDropzone from 'components/FalconDropzone';

////// CommonJS Helpers //////////
import base64toBlob from 'helpers/base64toBlob';
import getSizedImageName from 'helpers/getSizedImageName';
import { arrayBufferToBase64 } from 'helpers/arrayBufferToBase64';

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import {addPersonExperience, removePersonExperience} from 'store/thunks/people';
import { IExperience } from 'store/types/models/id';
import dayjs from 'dayjs';
import { toast } from 'react-toastify';



interface Props {
  personId: string;
  experienceId: string | undefined;
  setFormId: (formId: string)=>void;
  showDelete?: boolean;
}

const ExperienceForm: FC<Props> = ({ personId, experienceId = null, setFormId, showDelete = false }) => {
  const thunkExperience = useAction(addPersonExperience);
  const thunkRemoveExperience = useAction(removePersonExperience);
  const experience = useSelector(state => {
    if (Array.isArray(state.id.people?.[personId]?.experiences)) {
      return (
        state.id.people[personId]?.experiences?.find( v => experienceId && v._id === experienceId)
      )
    }
  }, isEqual)

  // State
  const [organization, setOrganization] = useState('');
  const [position, setPosition] = useState('');
  const [city, setCity] = useState('');
  const [current, setCurrent] = useState(false);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [isButtonDisable, setIsButtonDisable] = useState(true);
  const [avatar, setAvatar] = useState({} as Partial<{base64: string; name: string}>);

  function setForm(experienceObject: Partial<IExperience>) {
    setOrganization(experienceObject.organization || '');
    setPosition(experienceObject.position || '');
    setCity(experienceObject.location || '');
    setCurrent(!experienceObject.dateEnd);
    setStartDate(dayjs(experienceObject.dateStart).isValid() ? dayjs(experienceObject.dateStart).toISOString() : '');
    setEndDate(dayjs(experienceObject.dateEnd).isValid() ? dayjs(experienceObject.dateEnd).toISOString() : '');
    if (experienceObject.photo) {
      downloadAvatar(experienceObject.photo.location)
    } else {
      setAvatar({})
    }
  }

  useEffect( () => {
    if (experience) {
      setForm(experience)
    }
  }, [experience])

  async function downloadAvatar(src: string) {
    const response = await fetch(getSizedImageName(src, 'original'), {
      credentials: "include"
    })
    const responseData = await response.arrayBuffer();
    const photoData = arrayBufferToBase64(responseData);
    const extension = /(?:\.)(\w+)$/i.exec(src)?.[1]
    const photo = 'data:image/' + extension + ";base64," + photoData
    setAvatar({base64: photo, name: src})
  }
  const handleImageSubmit = useCallback(images => {
      setAvatar({
        base64: images[0].base64, 
        name: images[0].path
      })}, [setAvatar])

  const handleSubmit = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();

    const newExperience ={
      _id: experienceId,
      organization,
      photo: {
        data: avatar?.base64 && base64toBlob(avatar.base64),
        name: avatar.name
      },
      location: city,
      position,
      dateStart: startDate,
      dateEnd: endDate
    }

    const data = await thunkExperience.post(newExperience, personId);
    if (data?.success) {
      setFormId('none');
      setForm({})
    }

    
  };

  const handleCancel = useCallback(() => setFormId('none'), [setFormId])

  const handleDelete = async () => {
    console.log('starting')
    const data = await thunkRemoveExperience.post(experienceId, personId);
    console.log('completed')
    if (data?.success) {
      setFormId('none');
      toast.success('removed')
    } else {
      toast.error(data?.error || 'something went wrong')
    }
  }

  useEffect(() => {
    setIsButtonDisable(
      !(
        organization &&
        position &&
        city &&
        !isNaN(Date.parse(startDate)) &&
        (current || !isNaN(Date.parse(endDate)))
      )
    );
  }, [organization, position, city, current, startDate, endDate]);

  const handleStartDateChange = useCallback((date: string) => setStartDate(date), []);
  const handleEndDateChange = useCallback((date: string) => setEndDate(date), []);

  return (
    <Form>
      <Row>
        <Col xs={3} className="text-lg-right">
          <Label className="mb-0">
            Avatar
          </Label>
        </Col>
        <Col xs={9}>
          <FalconDropzone
            placeholder={ 
              avatar?.base64 && 
              <div className="avatar avatar-3xl" >
                <img src={avatar?.base64} className="rounded-circle " />
              </div> 
            }
            placeholderText={avatar?.base64 ? "" : "Upload Institution Photo"}
            multiple={false}
            accept="image/*"
            onChange={ handleImageSubmit }
          />
        </Col>
      </Row>

      <AsideFormInput
        id={`organization-${experienceId}`}
        label="Company"
        value={organization}
        onChange={({ target }) => setOrganization(target.value)}
        className="mt-3"
      />

      <AsideFormInput
        id={`position-${experienceId}`}
        label="Position"
        value={position}
        onChange={({ target }) => setPosition(target.value)}
      />

      <AsideFormInput id={`city-${experienceId}`} label="City" value={city} onChange={({ target }) => setCity(target.value)} />

      <FormGroup>
        <Row className="row">
          <Col lg={{ size: 9, offset: 3 }}>
            <CustomInput
              type="checkbox"
              id={`current-${experienceId}`}
              label="I currently work here"
              checked={current}
              onChange={({ target }) => {
                if (target.checked) {
                  setEndDate('')
                }
                setCurrent(target.checked)
              }}
              inline
            />
          </Col>
        </Row>
      </FormGroup>

      <AsideFormDate 
        id={`experienceFrom-${experienceId}`}
        label="From"
        value={startDate}
        dateFormat="MM/DD/YYYY"
        timeFormat={false}
        onChange={handleStartDateChange}
      />

      {!current && (
        <AsideFormDate
          id={`experienceTo-${experienceId}`}
          label="To"
          value={endDate}
          dateFormat="MM/DD/YYYY"
          timeFormat={false}
          onChange={handleEndDateChange}
        />
      )}
      {(thunkExperience.error || thunkRemoveExperience.error) &&
        <Alert color="danger">Something went wrong!</Alert>
      }
      <FormGroup className="form-group">
        <Row className="row">
          <Col lg={{ size: 3}}>
            {showDelete && 
              <Button color="danger" onClick={handleDelete}>
                <FontAwesomeIcon icon='trash-alt' />
              </Button>
            }

          </Col>
          <Col lg={{ size: 3, offset: 3 }}>
            <Button color="primary" disabled={isButtonDisable} onClick={handleSubmit}>
              <FontAwesomeIcon icon='check' />
            </Button>
          </Col>
          <Col lg={{ size: 3 }}>
          <Button color="secondary" onClick={handleCancel}>
              <FontAwesomeIcon icon='times' />
            </Button>
          </Col>
        </Row>
      </FormGroup>
    </Form>
  );
};



export default ExperienceForm;
