import { shallowEqual } from "react-redux"
import { getPersonContacts } from "store/thunks/people"
import useLoadAndSelect from "../useLoadAndSelect"

 function useContactsList( searchText: string ) {
     return useLoadAndSelect({
         actionCreator: getPersonContacts,
         postParams: searchText,
         selector: state => Object.values(state.id.people).map(p=>({...p, organizationName: state.id.organizations[p.organizationId]?.name})),
         equality: shallowEqual,
         deduplicate: ['people', searchText]
     })
     
 }

 export default useContactsList