////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////

////// Global Components /////////
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia';
import { MOrganization } from 'store/types/models/id';
////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
    organization: MOrganization;
    className: string;
}

const DisplayEmails: FC<Props> = ({organization, className}) => {
    return (
        <>
            {organization?.emails && organization.emails.length > 0 &&
                <AsideResponsiveMedia icon="envelope" className={className}>
                    <h6 className="mb-0 font-weight-bold">Email</h6>
                            {organization?.emails?.map( ({description, address}, index) => ( 
                                <h6 key={index} className="mb-0 text-truncate">
                                    <em className="pr-1 pt-1 d-block d-sm-inline pt-sm-0">
                                        {description}:
                                    </em> 
                                    <a className="text-900" href={`mailto:${address}`}>{address} </a> 
                                </h6>
                            ))}
                </AsideResponsiveMedia>
            }
        </>
    );
};


DisplayEmails.propTypes = {

};


export default DisplayEmails;
