import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { themeColors } from 'helpers/utils';
import React, { FC } from 'react'

interface Props {
    rating?: number;
    className?: string;
    setRating?: (rating: number) => void
}
const StarRating: FC<Props> = ({rating=0, className, setRating}) => (
    <div className={className}>
        {[1, 2, 3, 4, 5].map( (r, index) => <FontAwesomeIcon 
                                                icon={rating ? "star" : ["far", "star"]} 
                                                color={rating ? (rating >= r ? "rgb(245, 200, 66)" : themeColors['light']) : themeColors['secondary']} 
                                                key={index} 
                                                onClick={()=>setRating && setRating(r)}
                                                className={ setRating ? 'editable-star' : ''}
                                            /> )}
    </div>
)

export default StarRating