////// Packages //////////////////
import React, { useState, useCallback, FC } from 'react';
import { RouteComponentProps } from 'react-router';
import { useSelector } from 'react-redux';

////// Local Components //////////
import ProfileBanner from './ProfileBanner';
import ProfileAside from './ProfileAside';
import ProfileContent from './ProfileContent';
import ProfileFooter from './ProfileFooter';
import ProfileConfigurationModal from './ProfileConfigurationModal';

////// Global Components /////////
import ContentWithAsideLayout from 'layouts/ContentWithAsideLayout';
import LoadingGateway from 'components/LoadingGateway';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import usePerson from 'store/hooks/person/usePerson';
import useMyPermissions from 'store/hooks/user/useMyPermissions';
import useOrganization from 'store/hooks/organization/useOrganization';
import PERMISSIONS from 'store/permissions';
import useEvaluatePermissions from 'store/hooks/user/useEvaluatePermissions';

////// Thunks ////////////////////

interface Props extends RouteComponentProps<{personId: string}> {

}

const Profile: FC<Props> = ({match: {params}}) => {
  const { personId } = params

  const [openModal, setOpenModal] = useState(false);

  const toggleModal = useCallback(() => setOpenModal(!openModal), [setOpenModal, openModal]);

  const {personId: loginPerson, organizationId: loginOrganization} = useSelector(state => state.app.login);

  const editContactPermissions = useEvaluatePermissions([PERMISSIONS.EDIT_CONTACTS]);
  let selectedPersonId: string;
  let isEditable = false;

  if (personId === 'my') {
    selectedPersonId = loginPerson!;
    isEditable = true;
  } else {
    selectedPersonId = personId
  }

  const {value: person, ...personLoader} = usePerson(selectedPersonId);
  const {value: organization} = useOrganization(person?.organizationId || '')

  if (organization?.recordOwner === loginOrganization && editContactPermissions) {
    isEditable = true
  }

  return (
    <LoadingGateway dataLoader={personLoader} size="xl">
      <ContentWithAsideLayout 
        banner={ <ProfileBanner setOpenModal={setOpenModal} personId={selectedPersonId} isEditable={isEditable} /> } 
        content={ <ProfileContent person={person!} isEditable={isEditable} /> }
        aside={ <ProfileAside person={person!} isEditable={isEditable} /> } 
        footer={ <ProfileFooter /> }
      />
      <ProfileConfigurationModal person={person!} isOpen={openModal} toggle={toggleModal} />
    </LoadingGateway>
  );
};

export default Profile;
