////// Packages //////////////////
import React, { FC, useCallback, useState, useEffect } from 'react';
import { Input } from 'reactstrap';

////// Local Components //////////
import EditableSection from './EditableSection';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { ActionCreator, ApiInterface } from 'store/types/interface/common';

interface Props {
    text: string | undefined;
    renderLine?: (substr: string, index: number)=>React.ReactNode
    setter: ActionCreator<any>;
    inputType?: 'text' | 'textarea' | 'date';
    inputRows: number;
    setterParams?: any | any[];
}

const TextEditSection: FC<Props> =({text, setter, inputType = 'text', inputRows, renderLine=(str, i)=><p key={i}>{str}</p>, setterParams }) => {
    const thunk = useAction(setter)
    const [innerText, setInnerText] = useState(text);

    const handleTextChange = useCallback(e => setInnerText(e.target.value), [setInnerText]);

    const handleSubmit = async () => {
        const arrParams = Array.isArray(setterParams) ? setterParams : [setterParams]
        const data = await thunk.post(innerText, ...arrParams)
        return data?.success
    }

    useEffect(() => {
        setInnerText(text);
    }, [text])

    return (
        <EditableSection 
            baseContent={ text?.split("\n").map( renderLine ) }
            editContent={
                <>
                    <Input
                        type={ inputType }
                        rows={ inputRows }
                        value={ innerText }
                        onChange={ handleTextChange }
                        invalid={ !!thunk.error }
                    />
                    { thunk.error && 
                        <h6>
                            { thunk.error }
                        </h6>
                    }
                </>
            }
            handleSubmit={ handleSubmit }
            loading={ thunk.loading }
        />
    );
};


export default TextEditSection;

