////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import makeName from 'helpers/makeName';
import { PeerContext } from 'helpers/PeerContext';
import React, {FC, useContext, useState} from 'react';
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap';
import usePerson from 'store/hooks/person/usePerson';

////// Local Components //////////
import ItemBanner from './components/ItemBanner';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
  personId: string;
  isEditable?: boolean;
  setOpenModal: (open: boolean)=>void;
}

const ProfileBanner: FC<Props> = ({ personId, isEditable = false, setOpenModal }) => {
  const { startCall } = useContext(PeerContext)
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const {value: person} = usePerson(personId)
  const toggle = () => setDropdownOpen(prevState => !prevState)

  return (
    <ItemBanner>
      <ItemBanner.Header 
        personId={personId} 
        isEditable={isEditable} 
      />
      <ItemBanner.Body
        personId={personId}
        verified
        isEditable={isEditable}
        setOpenModal={setOpenModal}
      >
        <Dropdown isOpen={dropdownOpen} toggle={toggle} size='sm'>
          <DropdownToggle color='falcon-primary' className='mt-2'>
            Contact {makeName(person?.name, {first: true, common: true})}
          </DropdownToggle>
        
          <DropdownMenu>
              <DropdownItem className='py-2'>
                  <FontAwesomeIcon icon='envelope' className='mr-2 fa-xs text-primary'/>
                      Direct Message
              </DropdownItem>     
          </DropdownMenu>
        </Dropdown>
      </ItemBanner.Body>
    </ItemBanner>
  )
};

export default ProfileBanner;
