import React,{FC} from 'react';
import { Col, FormGroup, Input, InputProps, Label, Row } from 'reactstrap';

interface Props extends InputProps {
  id: string;
  label: string;
}

const AsideFormInput: FC<Props> = ({ id, label, type, className, ...rest }) => (
  <FormGroup className={`form-group ${className}`}>
    <Row>
      <Col lg={3} className="text-lg-right">
        <Label className="mb-0" htmlFor={id}>
          {label}
        </Label>
      </Col>
      <Col lg={9}>
        <Input bsSize="sm" id={id} type={type} {...rest} />
      </Col>
    </Row>
  </FormGroup>
);

export default AsideFormInput;
