////// Packages //////////////////
import React, { useCallback, useEffect, useState, FC } from 'react';
import {Button, Col, Row, Alert, FormGroup, InputGroupAddon, UncontrolledTooltip, InputGroup, Input} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';
import EditableSection from 'compositions/modal/EditableSection';
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import { useAction } from 'store/hooks/useAction';

////// Thunks ////////////////////
import {sendPersonEmails} from 'store/thunks/people';
import { useDispatch } from 'react-redux';

////// Types /////////////////////
import { IEmail } from 'store/types/models/id';

interface Props {
    emails: IEmail[] | undefined;
    personId: string;
}

const ModalEmails: FC<Props> = ({emails, personId}) => {
    const sendEmails = useAction(sendPersonEmails);

    const [newEmails, setNewEmails] = useState(emails || []);
    const [error, setError] = useState([])

    const dispatch = useDispatch();

    const handleEmailRemove = useCallback(index => () => {
        setNewEmails( newEmails.filter( (v, i) => i !== index) )   
    }, [newEmails]);

    const handleDescriptionChange = useCallback((index: number) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setNewEmails( newEmails.map( (v, i) => i === index ? {description: event.target.value, address: v.address} : v ))
    }, [newEmails])


    const handleAddressChange = useCallback((index: number) => (event: React.ChangeEvent<HTMLInputElement>) => {
 
        setNewEmails(newEmails.map( (v, i) => i === index ? {description: v.description, address: event.target.value} : v));
    }, [setNewEmails, newEmails]); 

    const handleEmailAdd = useCallback( () => setNewEmails( [...newEmails, {description: '', address: ''}] ) , [newEmails]);

    useEffect(() => {
        setNewEmails(emails || []);
    }, [emails]);
    
    const handleSubmit = useCallback(async () => {
        const data = await sendEmails.post(newEmails, personId);
        if (data?.success) {
            setError([]);
            return true;
        } else {
            return false;
        }
    }, [newEmails, personId, dispatch])

    return (
        <ModalMediaContent title="Email" icon="envelope">
            <EditableSection
                baseId="email"
                baseContent={
                    emails?.map(({ _id, description, address }) => (
                        <div key={_id}>
                            <strong><em>{description}: </em></strong> {address}
                        </div>
                    ))
                }
                editContent={
                    <>
                        {newEmails?.map(({ description, address }, index) => (
                            <>
                            <FormGroup className="btn-reveal-trigger" key={index}>
                                <Row noGutters>
                                     <Col>
                                     <InputGroup>
                                        <InputGroupAddon addonType="prepend">
                                                <Button color="link" className="py-0 px-2 text-danger" onClick={handleEmailRemove(index)} id={`email-${index}-profile`}>
                                                    <FontAwesomeIcon icon="trash" />
                                                </Button>
                                                <UncontrolledTooltip placement="bottom" target={`email-${index}-profile`}>
                                                        Delete Email
                                                </UncontrolledTooltip>
                                            </InputGroupAddon>
                                        <Input
                                            id={`email-description-${index}`}
                                            label=''
                                            value={description}
                                            onChange={handleDescriptionChange(index)}
                                            placeholder='Type (i.e. "Main")'
                                            type="text"
                                            name="email-type"
                                        />
                                     </InputGroup>
                                    </Col>
                                    
                                    <Col>
                                        <FormGroupInput 
                                            id={`email-address-${index}`}
                                            label=''
                                            value={address}
                                            onChange={handleAddressChange(index)}
                                            invalid={error[index]}
                                            placeholder="Address"
                                            type="email"
                                        />
                                        <Row key={`${index}-error`}>
                                            <Col>
                                                {error[index] && <Alert color="danger" >{error[index]}</Alert>}
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                                   
                            </FormGroup>
                            </>
                        ))}
                        <Row>
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handleEmailAdd} id="add-email-now">
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                                <UncontrolledTooltip placement="bottom" target="add-email-now">
                                    Add email
                                </UncontrolledTooltip>
                            </Col>
                        </Row>
                    </>

                }
                handleSubmit={handleSubmit}
                />
        </ModalMediaContent>
    )
    
};


export default ModalEmails;