////// Packages //////////////////
import React, { useCallback, useEffect, useState, FC } from 'react';
import {Button, Col, Row, Alert} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';

////// Global Components /////////
import EditableSection from 'compositions/modal/EditableSection';
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import {sendOrgWebsites} from 'store/thunks/organizations';

interface Props {
    websites?: string[];
    organizationId: string;
}

const ModalWebsites: FC<Props> = ({websites, organizationId}) => {
    const setWebsites = useAction(sendOrgWebsites);

    const [newWebsites, setNewWebsites] = useState(websites || []);

    const handleWebsiteRemove = useCallback(index => () => {
        setNewWebsites( newWebsites.filter( (v, i) => i !== index) )   
    }, [newWebsites]);

    const handleWebsiteChange = useCallback((index: number) => ( event: React.ChangeEvent<HTMLInputElement> ) => {
        setNewWebsites( newWebsites.map( (v, i) => i === index ? event.target.value : v ))
    }, [newWebsites])

    const handleWebsiteAdd = useCallback( () => setNewWebsites( [...newWebsites, ''] ) , [newWebsites]);

    useEffect(() => {
        setNewWebsites(websites || []);
    }, [websites]);
    
    const handleSubmit = useCallback(async () => {
        const data = await setWebsites.post(newWebsites, organizationId);
        return data?.success
    }, [newWebsites, setWebsites.post, organizationId])

    return (
        <ModalMediaContent title="Websites" icon="globe-americas">
            <EditableSection
                baseContent={
                    websites?.map( (website, index) => {
                        const pattern = /\/\//
                        const websiteLink = pattern.test(website) ? website : 'http://' + website
                        return (
                            <div key={index} className="mb-0">
                                <a href={websiteLink} target="_blank" rel="noreferrer">
                                    {website} 
                                </a>
                            </div>
                        )
                    })
                }
                editContent={
                    <>
                        {newWebsites?.map((website, index) => (
                            <>
                                <Row key={index} className="no-gutters btn-reveal-trigger">
                                    <Col xs="auto">
                                        <Button color="link" className="btn-reveal py-0 px-2" onClick={handleWebsiteRemove(index)}>
                                            <FontAwesomeIcon icon="trash" />
                                        </Button>
                                    </Col>
                                    <Col xs="auto">
                                        <FormGroupInput
                                            id={`website-${index}`}
                                            label=''
                                            value={website}
                                            onChange={handleWebsiteChange(index)}
                                            placeholder='Web Address'
                                            type="url"
                                        />
                                    </Col>
                                </Row>

                            </>
                        ))}
                        <Row>
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handleWebsiteAdd}>
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                            </Col>
                        </Row>
                        {setWebsites.error && 
                            <Alert color="danger">{setWebsites.error}</Alert>
                        }
                    </>

                }
                handleSubmit={handleSubmit}
                loading={setWebsites.loading}
            />
        </ModalMediaContent>
    )
    
};


export default ModalWebsites;