import React, {FC} from 'react';
import { Card, CardBody } from 'reactstrap';

interface Props {
    organizationId: string
}

const OrganizationFooter: FC<Props> = ({organizationId}) => {
    return (
        <Card>
            <CardBody>
                Organization Footer
            </CardBody>
        </Card>
    );
};


export default OrganizationFooter;
