////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////
import ProfileIntro from './ProfileIntro';
import Photos from './components/Photos';
import ActivityLog from './components/ActivityLog';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';

interface Props {
  person: MPerson;
  isEditable: boolean;
}

const ProfileContent: FC<Props> = ({person, isEditable=false}) => {
  return (
    <>
      <ProfileIntro person={person} isEditable={isEditable} />
      <Photos className="mb-3 mb-lg-3" person={person} />
      <ActivityLog person={person} />
    </>
  );
};


export default ProfileContent;