import { union } from 'lodash'
import { useSelector, shallowEqual } from 'react-redux'
import useMyUser from './useMyUser';
 
function useMyPermissions (): string[] { 
    const {value: user} = useMyUser();

    const myPermissions = useSelector(state => {
        const explicitPermissions = user?.explicitPermissions;
        const permissionsGroup = user?.groupId;
        const groupPermissions = state.id.usergroups?.[permissionsGroup || '']?.permissions;
        return union(explicitPermissions, groupPermissions);
    }, shallowEqual);

    return myPermissions
}

export default useMyPermissions;