////// Packages //////////////////
import React, { FC, useState } from 'react';
import { Input, Alert } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import EditableSection from 'compositions/modal/EditableSection';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendOrgName } from 'store/thunks/organizations';

interface Props {
    organizationName: string;
    organizationId: string;
}

const ModalOrgName: FC<Props> = ({organizationName, organizationId}) => {
    const setName = useAction(sendOrgName);

    const [orgName, setOrgName] = useState(organizationName);

    const handleSubmitOrgName = async () => {
        const data = await setName.post(orgName, organizationId)
        return data?.success
    }

    return (
        <h4 className="mb-3">
            <EditableSection
                baseContent={organizationName}
                editContent={
                    <>
                        <Input
                            type="text"
                            value={orgName}
                            onChange={e => setOrgName(e.target.value)}
                        />
                        {setName.error && 
                            <Alert color='danger'>{setName.error}</Alert>
                        }
                    </>
                }
                handleSubmit={handleSubmitOrgName}
                loading={setName.loading}
            />
        </h4>
    );
};


export default ModalOrgName;
