////// Packages //////////////////
import React, { useCallback, useMemo, useState, FC } from 'react'
import { Button, Card, CardBody, Col, Modal, ModalBody, ModalFooter, ModalHeader, Row, UncontrolledTooltip } from 'reactstrap';
import { SRLWrapper } from 'simple-react-lightbox';

////// Local Components //////////

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import FalconDropzone from 'components/FalconDropzone';
import ProgressiveImage from 'components/ProgressiveImage';
import { DropzoneFile } from 'store/store';
import BounceSpinner from 'components/BounceSpinner'

////// CommonJS Helpers //////////
import getSizedImageName from 'helpers/getSizedImageName';

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { IPhoto } from 'store/types/models/id';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


interface Props {
    photos: IPhoto[];
    onSubmitPhotos: (images: DropzoneFile[])=>Promise<boolean>;
    className?: string;
  }

  const Photos: FC<Props> = ({photos, onSubmitPhotos, className}) => {
    
    const [ showModal, setShowModal ] = useState(false);
    const [ modalImages, setModalImages ] = useState([] as DropzoneFile[]);
    const [ selectedIndex, setSelectedIndex] = useState(999);
    const [loading, setLoading] = useState(false);

    const maxLength = Math.max(5, selectedIndex + 2);
  
    const toggleModal = () => setShowModal(!showModal);
    const imageStyle = {objectFit: 'cover', borderRadius: '.5rem'};
  
    const onSlideChange = useCallback( ({index}) => setSelectedIndex(index), [setSelectedIndex]);
    const onLightboxOpened = useCallback( ({currentSlide}) => setSelectedIndex(parseInt(currentSlide.id)), [setSelectedIndex]);
  
    const callbacks = useMemo(() => ({onSlideChange, onLightboxOpened}), [onSlideChange, onLightboxOpened])
  
    const onSubmit = async () => {
      setLoading(true);
      const closeModal = await onSubmitPhotos(modalImages);
      setLoading(false);
      setShowModal(!closeModal);
    }
  
    return (
      <Card className={className}>
        <FalconCardHeader title="Photos">
          <Button color="link" onClick={toggleModal} className="text-sans-serif" id="add-profile-photos">
            <FontAwesomeIcon icon="plus"/>
          </Button>
          <UncontrolledTooltip placement="top" target="add-profile-photos">
              Add Photos
          </UncontrolledTooltip>
          <Modal isOpen={showModal} toggle={toggleModal}>
            <ModalHeader className="bg-light border-bottom-0">
              Add Photos
            </ModalHeader>
            <ModalBody>
              <FalconDropzone
                multiple={true}
                accept="image/*"
                onChange={(files: DropzoneFile[]) => setModalImages([...modalImages, ...files]) }
              />
              <div>
                {modalImages.map( (image, index) => (
                  <div className="d-inline-block position-relative m-3" key={index}>
                    <img src={image.base64} alt='' width={75} height={100} style={imageStyle} />
                    <button 
                      className="close position-absolute" 
                      style={{top: 0, right: 5}} 
                      type="button" 
                      aria-label="Close" 
                      onClick={() => setModalImages(modalImages.filter(img => img.id !== image.id))}
                    >
                      <span aria-hidden="true">×</span>
                    </button>
                  </div>
                ))}
              </div>
              
            </ModalBody>
            {modalImages.length > 0 && 
              <ModalFooter className="bg-light border-top-0">
                  <div className="d-flex">
                    <Button size='sm'color="primary" onClick={onSubmit} className="ml-auto">
                      Submit
                      {loading && <BounceSpinner color="light" size="sm" />}
                    </Button>
                  </div>
              </ModalFooter>
            }
          </Modal>
        </FalconCardHeader>
        <CardBody>
          <SRLWrapper callbacks={callbacks}>
            <Row noGutters className="m-n1">
              {photos && 
                photos.filter((v, i) => i < maxLength).map( (image, index, arraySelf) => (
                  <Col xs={index < 2 ? 6 : 4} lg={index < 2 ? 4 : 3} className={`p-1 ${index > 4 ? 'd-none' : ''}`} style={ {maxHeight: '20vh'} } key={index} >
                    <a href={getSizedImageName(image?.location, 'original')} data-attribute='SRL'>
                      <ProgressiveImage
                        style={{objectFit: 'cover', borderRadius: '.5rem'}}
                        className="rounded w-100 h-100"
                        src={image?.location}
                        size="l"
                        alt=""
                      />
                    </a>
                  </Col>
                )
              )}
            </Row>
          </SRLWrapper>
        </CardBody>
      </Card>
    )
  };
  
  export default Photos;