////// Packages //////////////////
import React, { useCallback, useEffect, useState, FC } from 'react';
import {Button, Col, Row, Alert, UncontrolledTooltip, FormGroup, InputGroup, InputGroupAddon, Input} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import ModalMediaContent from 'compositions/modal/ModalMediaContent';

////// Global Components /////////
import EditableSection from 'compositions/modal/EditableSection';
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendOrgEmails } from 'store/thunks/organizations';

////// Types /////////////////////
import { IEmail } from 'store/types/models/id';
import validEmailRegex from 'helpers/validEmailRegex';

interface Props {
    emails?: IEmail[];
    organizationId: string;
}

const ModalEmails: FC<Props> = ({emails, organizationId}) => {
    
    const setEmails = useAction(sendOrgEmails)

    const [newEmails, setNewEmails] = useState(emails || []);

    const handleEmailRemove = useCallback(index => () => {
        setNewEmails( newEmails.filter( (v, i) => i !== index) )   
    }, [newEmails]);

    const handleDescriptionChange = useCallback((index: number) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setNewEmails( newEmails.map( (v, i) => i === index ? {description: event.target.value, address: v.address} : v ))
    }, [newEmails])


    const handleAddressChange = useCallback((index: number) => (event: React.ChangeEvent<HTMLInputElement>) => {
 
        setNewEmails(newEmails.map( (v, i) => i === index ? {description: v.description, address: event.target.value} : v));
    }, [setNewEmails, newEmails]); 

    const handleEmailAdd = useCallback( () => setNewEmails( [...newEmails, {description: '', address: ''}] ) , [newEmails]);

    useEffect(() => {
        setNewEmails(emails || []);
    }, [emails]);
    
    const handleSubmit = useCallback(async () => {
        const data = await setEmails.post(newEmails, organizationId);
        return data?.success
    }, [newEmails, setEmails.post, organizationId])

    return (
        <ModalMediaContent title="Email" icon="envelope">
            <EditableSection
                baseId="email-org"
                baseContent={
                    emails?.map(({ _id, description, address }) => (
                        <div key={_id}>
                            <strong><em>{description}: </em></strong> {address}
                        </div>
                    ))
                }
                editContent={
                    <>
                        {newEmails?.map(({ description, address }, index) => (
                            <>
                            <FormGroup className="btn-reveal-trigger" key={index}>
                                <Row noGutters>
                                     <Col>
                                     <InputGroup>
                                        <InputGroupAddon addonType="prepend">
                                                <Button color="link" className="py-0 px-2 text-danger" onClick={handleEmailRemove(index)} id={`email-${index}-organization`}>
                                                    <FontAwesomeIcon icon="trash" />
                                                </Button>
                                                <UncontrolledTooltip placement="bottom" target={`email-${index}-organization`}>
                                                        Delete Email
                                                </UncontrolledTooltip>
                                            </InputGroupAddon>
                                        <Input
                                            id={`email-description-org-${index}`}
                                            label=''
                                            value={description}
                                            onChange={handleDescriptionChange(index)}
                                            placeholder='Type (i.e. "Main")'
                                            type="text"
                                            name="email-type"
                                        />
                                     </InputGroup>
                                    </Col>
                                    
                                    <Col>
                                        <FormGroupInput 
                                            id={`email-address-${index}`}
                                            label=''
                                            value={address}
                                            onChange={handleAddressChange(index)}
                                            valid={validEmailRegex.test(address)}
                                            placeholder="Address"
                                            type="email"
                                        />
                                    </Col>
                                </Row>
                                   
                            </FormGroup>
                            </>
                        ))}
                        <Row>
                            <Col xs="auto">
                                <Button color="link" className="btn-reveal py-0 px-2" onClick={handleEmailAdd} id="add-email-org">
                                    <FontAwesomeIcon icon="plus" />
                                </Button>
                                <UncontrolledTooltip placement="bottom" target="add-email-org">
                                    Add email
                                </UncontrolledTooltip>
                            </Col>
                        </Row>
                        {setEmails.error && 
                            <Alert color="danger">{setEmails.error}</Alert>
                        }
                    </>

                }
                handleSubmit={handleSubmit}
                loading={setEmails.loading}
            />
        </ModalMediaContent>
    )
    
};


export default ModalEmails;