////// Packages //////////////////
import React, {FC} from 'react';
import { Card, CardBody } from 'reactstrap';

////// Local Components //////////
import DisplayPhones from './components/DisplayPhones';
import DisplayEmails from './components/DisplayEmails';
import DisplayAddresses from './components/DisplayAddresses';
import DisplayWebsites from './components/DisplayWebsites';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import LoadingGateway from 'components/LoadingGateway';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useOrganization from 'store/hooks/organization/useOrganization';

////// Thunks ////////////////////

interface Props {
    organizationId: string;
}
const OrganizationAside: FC<Props> = ({organizationId}) => {
    const {value: organization, ...organizationLoader} = useOrganization(organizationId);

    return (
        <Card className="mb-3">
            <FalconCardHeader title="Contact" light={true} />
            <CardBody >
                <LoadingGateway dataLoader={organizationLoader} >
                    {organization &&
                    <>
                        <DisplayPhones organization={organization} className=" "/>
                        <DisplayEmails organization={organization} className="mt-3" />
                        <DisplayAddresses organization={organization} className="mt-3" />
                        <DisplayWebsites organization={organization} className="mt-3" />
                    </>
                    }
                    
                </LoadingGateway>
            </CardBody>
        </Card>
    );
};


export default OrganizationAside;
