import { shallowEqual, useSelector } from 'react-redux';
import { people } from 'store/thunks/people';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect';
import { MPerson } from 'store/types/models/id';

function useOrgEmployees(organizationId?: string) {
    const myOrgId = useSelector( state => state.app.login.organizationId )
    const selectedOrg = organizationId || myOrgId;

    const peopleBasicInfo = useLoadAndSelect<MPerson[]>({
        actionCreator: people.getOrgEmployees,
        selector: state => {
            const personList = Object.values(state.id.people)
            const result = personList.filter( person => person.organizationId === selectedOrg )
            return result
        },
        postParams: selectedOrg,
        deduplicate: ['getOrgEmployees', selectedOrg]
    })

    return peopleBasicInfo;
}

export default useOrgEmployees;