////// Packages //////////////////
import React, { useCallback, useRef, FC } from 'react';
import {
    Button,
    Col,
    Input,
    Label,
    Modal,
    ModalBody,
    ModalHeader,
    Row,
    FormGroup,
    UncontrolledTooltip
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { toast } from 'react-toastify';

////// Local Components //////////
import ModalPhones from './components/ModalPhones';
import ModalEmails from './components/ModalEmails';

////// Global Components /////////
import Avatar from "components/Avatar";
import ProgressiveImage from 'components/ProgressiveImage';
import TextEditSection from 'compositions/modal/TextEditSection';
import ModalMediaContent from 'compositions/modal/ModalMediaContent';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import {
    sendPersonBio,
    sendPersonAvatar,
    sendPersonCover
} from 'store/thunks/people';

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';
import LoadingGateway from 'components/LoadingGateway';


interface Props {
    isOpen: boolean;
    toggle: () => void;
    person: MPerson;
}

const ProfileConfigurationModal: FC<Props> = ({ isOpen, toggle, person }) => {
    const sendCover = useAction(sendPersonCover, 15000);
    const sendAvatar = useAction(sendPersonAvatar, 15000);
    const avatarInput = useRef<HTMLInputElement>(null);

    const handleAvatarEdit = useCallback(() => avatarInput.current?.click(), [])

    const handleCoverPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files) return null;
        const data = await sendCover.post(event.target.files[0], person?._id)
        if (!data) toast.error('Failed to upload. Please try again')
    }

    const handleAvatarPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files) return null;
        const data = await sendAvatar.post(event.target.files[0], person?._id)
        if (!data) toast.error('Failed to upload. Please try again')
    }

    return (
        <Modal
            isOpen={isOpen}
            toggle={toggle}
            className={`mt-6 border rounded`}
            contentClassName="border-0"
            modalClassName="theme-modal"
            size="lg"
        >
            <ModalHeader toggle={toggle} className="bg-light border-bottom-0">Edit Profile</ModalHeader>
            <div>
                <div className="position-absolute mt-1 ml-1 z-index-1">
                    <Input
                        className="d-none"
                        id="upload-cover-image"
                        type="file"
                        accept=".jpg,.jpeg,.png,.heic,.tiff"
                        onChange={handleCoverPhoto}
                    />
                    <Label className="cover-image-file-input opacity-1" htmlFor="upload-cover-image" style={{ whiteSpace: "nowrap" }}>
                        <FontAwesomeIcon icon="camera" className="mr-2" />
                        <span>Change cover photo</span>
                    </Label>
                </div>
                <LoadingGateway dataLoader={sendCover}>
                    <ProgressiveImage
                        src={person?.coverPhoto?.location}
                        width="100%"
                        height="0"
                        style={{ objectFit: 'cover' }}
                        className="min-vh-25 rounded-top-0"
                    />
                </LoadingGateway>
            </div>
            <ModalBody className="px-4">
                <ModalMediaContent title="Profile Picture" icon="camera" className="pt-2" headingClass="pb-2">
                    <Row className="btn-reveal-trigger">
                        <Col>
                            <div className="d-flex justify-content-center">
                                <LoadingGateway dataLoader={sendAvatar}>
                                    <Avatar
                                        size="5xl"
                                        src={person?.avatar?.location}
                                        name={person?.name?.first + ' ' + person?.name?.last}
                                    />
                                </LoadingGateway>
                            </div>
                        </Col>
                        <Col xs={12} sm="auto" className="ml-sm-auto">
                            <FormGroup>
                                <Input
                                    type="file"
                                    innerRef={avatarInput}
                                    id="new-avatar-photo"
                                    className="d-none"
                                    accept=".jpg,.jpeg,.png,.heic,.tiff"
                                    onChange={handleAvatarPhoto}
                                />
                                <Label htmlFor="new-avatar-photo">
                                    <Button color="link" className="ml-2 btn-reveal py-0 px-1" onClick={handleAvatarEdit} id="edit-profile-avatar">
                                        <FontAwesomeIcon icon="pencil-alt" />
                                    </Button>
                                    <UncontrolledTooltip placement="bottom" target="edit-profile-avatar">
                                        Edit
                                    </UncontrolledTooltip>
                                </Label>
                            </FormGroup>
                        </Col>
                    </Row>
                </ModalMediaContent>
                <ModalMediaContent title="Bio" icon="book" className=" " headingClass=" ">
                    <TextEditSection
                        inputType="textarea"
                        inputRows={13}
                        text={person?.bio}
                        setter={useCallback(bio => sendPersonBio(bio, person?._id), [person?._id])}
                        tag="p"
                    />
                </ModalMediaContent>
                <ModalPhones
                    phones={ person?.phones }
                    personId={ person?._id }
                />
                <ModalEmails
                    emails={ person?.emails }
                    personId={ person?._id }
                />


                {/* <div className="position-absolute t-0 r-0  z-index-1">
                    <Button
                        size="sm"
                        className="close close-circle d-flex flex-center transition-base mt-3 mr-3"
                        onClick={() => toggle()}
                    >
                        <FontAwesomeIcon icon="times" transform="shrink-6 right-0.3 down-0.3" />
                    </Button>
                </div>
                <div className="p-4">
                    <Row>
                        <Col lg="9">
                            <ModalMediaContent title="Reviewers" icon="user">
                                <GroupMember users={users} addMember avatarSize="xl" />
                            </ModalMediaContent>
                            <ModalMediaContent title="Labels" icon="tag">
                                <ModalLabelContent />
                            </ModalMediaContent>
                            <ModalMediaContent title="Description" icon="paperclip">
                                <p className="text-word-break fs--1">
                                    The illustration must match to the contrast of the theme. The illustraion must described the concept
                  of the design. To know more about the theme visit the page.{' '}
                                    <a
                                        href="https://prium.github.io/falcon/undefined/default/home/dashboard-alt.html"
                                        target="_blank"
                                        rel="noopener noreferrer"
                                    >
                                        https://prium.github.io/falcon//default/home/dashboard-alt.html
                  </a>
                                </p>
                            </ModalMediaContent>
                            <ModalMediaContent
                                title="Attachments"
                                icon="paperclip"
                                headingClass="d-flex justify-content-between"
                                headingContent={
                                    <UncontrolledButtonDropdown direction="right">
                                        <DropdownToggle caret size="sm" color="falcon-default" className="dropdown-caret-none fs--2">
                                            <FontAwesomeIcon icon="plus" />
                                        </DropdownToggle>
                                        <DropdownMenu>
                                            <DropdownItem>Computer</DropdownItem>
                                            <DropdownItem>Google Drive</DropdownItem>
                                            <DropdownItem divider />
                                            <DropdownItem>Attach Link</DropdownItem>
                                        </DropdownMenu>
                                    </UncontrolledButtonDropdown>
                                }
                            >
                                <ModalAttachmentsContent />
                            </ModalMediaContent>
                            <ModalMediaContent title="Comments" icon={['far', 'comment']} headingClass="mb-3">
                                <ModalCommentContent />
                            </ModalMediaContent>
                            <ModalMediaContent title="Activity" icon="list-ul" headingClass="mb-3" isHr={false}>
                                <ModalActivityContent />
                            </ModalMediaContent>
                        </Col>
                        <Col lg="3">
                            <ModalSideContent />
                        </Col> 
                    </Row>
                </div>*/}
            </ModalBody>
        </Modal>
    );
};

export default ProfileConfigurationModal;