////// Packages //////////////////
import React, { useRef, useCallback, FC } from 'react';
import {
    Button,
    Col,
    Input,
    Label,
    Modal,
    ModalBody,
    ModalHeader,
    Row,
    FormGroup,
    Alert
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { toast } from 'react-toastify';
import dayjs from 'dayjs';

////// Local Components //////////

import ModalOrgName from './components/ModalOrgName';
import ModalTagline from './components/ModalTagline';
import ModalPhones from './components/ModalPhones';
import ModalEmails from './components/ModalEmails';
import ModalWebsites from './components/ModalWebsites';
import ModalMailingAddress from './components/ModalMailingAddress'
////// Global Components /////////
import Avatar from "components/Avatar";
import ProgressiveImage from 'components/ProgressiveImage';
import TextEditSection from 'compositions/modal/TextEditSection';
import ModalMediaContent from 'compositions/modal/ModalMediaContent';
import DateEditSection from 'compositions/modal/DateEditSection';
import Loader from 'components/Loader';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useOrganization from 'store/hooks/organization/useOrganization';
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { 
    sendOrgFounding, 
    sendOrganizationAvatar, 
    sendOrgBio, 
    sendOrganizationCover 
} from 'store/thunks/organizations';



interface Props {
    isOpen: boolean;
    toggle: ()=>void;
    organizationId: string;
}

const OrgConfigurationModal: FC<Props> = ({ isOpen, toggle, organizationId }) => {

    const newAvatar = useAction(sendOrganizationAvatar);
    const newCover = useAction(sendOrganizationCover);

    const { value: organization, ...organizationLoader } = useOrganization(organizationId);

    const avatarInput = useRef<HTMLInputElement>();

    const handleAvatarEdit = useCallback(() => avatarInput.current?.click(), [avatarInput.current])

    const handleCoverPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files) return null;
        const data = await newCover.post(event.target.files[0], organizationId);
        if (!data?.success) {toast.error('Failed to upload. Please try again')}
    }

    const handleAvatarPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files) return null;
        const data = await newAvatar.post(event.target.files[0], organizationId);
        if (!data?.success) {toast.error('Failed to upload. Please try again')}
    }

    return (
        <Modal
            isOpen={isOpen}
            toggle={toggle}
            className={`mt-6`}
            contentClassName="border-0"
            modalClassName="theme-modal"
            size="lg"
        >
            <ModalHeader toggle={toggle} className="border-bottom-0 bg-light">Edit Organization</ModalHeader>
                <div>
                    <div className="position-absolute mt-1 ml-1 z-index-1">
                        <Input
                            className="d-none"
                            id="upload-cover-image"
                            type="file"
                            accept=".jpg,.jpeg,.png,.heic,.tiff"
                            onChange={handleCoverPhoto}
                        />
                        <Label className="cover-image-file-input opacity-1" htmlFor="upload-cover-image" style={{ whiteSpace: "nowrap" }}>
                            <FontAwesomeIcon icon="camera" className="mr-2" />
                            <span>Change cover photo</span>
                        </Label>
                    </div>
                    {newCover.loading ?
                            <Loader />
                        :
                            <ProgressiveImage
                                src={organization?.coverPhoto?.location || ''}
                                width="100%"
                                height="0"
                                style={{ objectFit: 'cover' }}
                                className="min-vh-25 rounded-top-0"
                            />
                    }
                    {newCover.error &&
                        <div className="error-overlay">
                            <Alert color="danger">{newCover.error}</Alert>
                        </div>
                    }
                </div>

                <div className="px-4 py-3 bg-light">
                    <ModalOrgName
                        organizationName={organization?.name || "Loading..."}
                        organizationId={organizationId}
                    />
                    <ModalTagline
                        tagline={organization?.tagline || ""}
                        organizationId={organizationId}
                    />
                </div>
            <ModalBody className="px-4">
                <ModalMediaContent title="Company Logo" icon="camera" className="pt-2" headingClass="pb-2">
                    <Row className="btn-reveal-trigger">
                        <Col>
                            <div className="d-flex justify-content-center">
                                <Avatar
                                    size="5xl"
                                    src={organization?.avatar?.location}
                                    name={organization?.name}
                                />
                            </div>
                        </Col>
                        <Col xs={12} sm="auto" className="ml-sm-auto">
                            <FormGroup>
                                <Input
                                    type="file"
                                    innerRef={avatarInput}
                                    id="new-avatar-photo"
                                    className="d-none"
                                    accept=".jpg,.jpeg,.png,.heic,.tiff"
                                    onChange={handleAvatarPhoto}
                                />
                                <Label htmlFor="new-avatar-photo">
                                    <Button color="link" className="ml-2 btn-reveal py-0 px-1" onClick={handleAvatarEdit}>
                                        <FontAwesomeIcon icon="pencil-alt" />
                                    </Button>
                                </Label>
                            </FormGroup>

                        </Col>
                    </Row>
                </ModalMediaContent>
                <ModalMediaContent title="About" icon="book">
                    <TextEditSection
                        inputType="textarea"
                        inputRows={13}
                        text={organization?.bio}
                        setter={sendOrgBio}
                        setterParams={organizationId}
                        tag="p"
                    />
                </ModalMediaContent>
                <ModalMediaContent title="Founding" icon="landmark">
                    <DateEditSection
                        date={organization?.foundingDate ?
                            dayjs(organization?.foundingDate).format('MMMM D, YYYY')
                            :
                            'Founding unknown'
                        }
                        setter={sendOrgFounding}
                        setterParams={organizationId}
                        dateFormat="MMMM D, YYYY"
                        timeFormat={false}
                    />
                </ModalMediaContent>
                <ModalPhones
                    phones={organization?.phones}
                    organizationId={organizationId}
                />
                <ModalMailingAddress
                    addresses={organization?.addresses}
                    organizationId={organizationId}
                />
                <ModalEmails
                    emails={organization?.emails}
                    organizationId={organizationId}
                />
                <ModalWebsites
                    websites={organization?.websites}
                    organizationId={organizationId}
                />

            </ModalBody>
        </Modal>
    );
};

export default OrgConfigurationModal;
