////// Packages //////////////////
import React, { FC, useState } from 'react'
import { Button, FormGroup, Label, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import { useHistory } from 'react-router'

////// Local Components //////////

////// Global Components /////////
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup'
import FormGroupInput from 'components/FormGroupInput'
import StarRating from 'components/StarRating'

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction'

////// Thunks ////////////////////
import { createContactOrganization } from 'store/thunks/organizations'
import { toast } from 'react-toastify'


////// Types /////////////////////

interface Props {
    isOpen: boolean, 
    setIsOpen: React.Dispatch<React.SetStateAction<boolean>>
    shouldRedirect?: boolean;
}

const AddOrganizationModal: FC<Props> = ({isOpen, setIsOpen, shouldRedirect}) => {
    const history = useHistory()
    const [name, setName] = useState('')
    const [tagline, setTagline] = useState('');
    const [bio, setBio] = useState('');
    const [foundingDate, setFoundingDate] = useState('');
    const [rating, setRating] = useState(undefined as undefined | number)
    const createOrgAction = useAction( createContactOrganization )

    async function handleCreateOrg() {
        const data = await createOrgAction.post(name, tagline, bio, foundingDate, rating);
        if (data?.success) {
            toast.success('Created ' + name)
            const newOrg = data?.records?.[0]?.organizations?.[0]
            if (newOrg && shouldRedirect) {
                history.push('/social/organizations/' + newOrg._id)
            }
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }
    return (
        <Modal isOpen={isOpen} toggle={()=>setIsOpen(false)}>
            <ModalHeader className="bg-light border-bottom-0">
                Add New Organization
            </ModalHeader>
            <ModalBody>
                <FormGroupInput
                    id="new-organization-name"
                    label="Name"
                    placeholder="Enter name..."
                    value={name}
                    onChange={e=>setName(e.target.value)}
                />
                <FormGroupInput
                    label='Tagline'
                    id='new-organization-tagline'
                    value={tagline}
                    onChange={e=>setTagline(e.target.value)}
                    placeholder="Enter tagline..."
                />
                <FormGroupInput
                    label='Bio'
                    id='new-organization-bio'
                    type="textarea"
                    rows={4}
                    value={bio}
                    onChange={e=>setBio(e.target.value)}
                />
                <FormGroup>
                    <Label>
                        Founding Date
                    </Label>
                    <DateTimeInputGroup
                        value={foundingDate}
                        onChange={date=>setFoundingDate(date)}
                        timeFormat={false}
                    />
                </FormGroup>
                <FormGroup>
                    <Label>Rating</Label>
                    <StarRating
                        rating={rating}
                        setRating={setRating}
                    />
                </FormGroup>
            </ModalBody>
            <ModalFooter className="bg-light border-top-0">
                <Button size='sm' color='primary' className='ml-auto' onClick={handleCreateOrg}>
                    Create
                </Button>
            </ModalFooter>
        </Modal>
    )
}

export default AddOrganizationModal

