////// Packages //////////////////
import React, { useState, useEffect, useCallback, FC } from 'react';
import { Alert, Button, Col, CustomInput, Form, FormGroup, Row, Label } from 'reactstrap';
import {useDispatch, useSelector} from 'react-redux';
import {isEqual} from 'lodash';
import dayjs from 'dayjs';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import AsideFormInput from 'compositions/AsideFormInput';
import AsideFormDate from 'compositions/AsideFormDate';

////// Global Components /////////
import FalconDropzone from 'components/FalconDropzone';

////// CommonJS Helpers //////////
import base64toBlob from 'helpers/base64toBlob';

////// Custom Hooks //////////////
import { useAction } from 'store/hooks/useAction';

////// Thunks ////////////////////
import { addPersonEducation, removePersonEducation } from 'store/thunks/people';
import { IEducation } from 'store/types/models/id';
import { arrayBufferToBase64 } from 'helpers/arrayBufferToBase64';
import getSizedImageName from 'helpers/getSizedImageName';

interface Props {
  personId: string;
  educationId?: string;
  setFormId: (formId: string)=>void;
  showDelete?: boolean;
}

const EducationForm: FC<Props> = ({ personId, educationId = null, setFormId, showDelete = false }) => {
  const dispatch = useDispatch();
  const addEducation = useAction(addPersonEducation)
  const removeEducation = useAction(removePersonEducation)
  const education = useSelector(state => {
    if (Array.isArray(state.id.people?.[personId]?.educations)) {
      return (
        state.id.people[personId].educations!.find( v => v._id === educationId)
      )
    }
  }, isEqual)

  // State
  const [institution, setInstitution] = useState('');
  const [degree, setDegree] = useState('');
  const [city, setCity] = useState('');
  const [current, setCurrent] = useState(false);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [isButtonDisable, setIsButtonDisable] = useState(true);
  const [avatar, setAvatar] = useState({} as Partial<{base64: string, name: string}>);

  function setForm(educationObject: Partial<IEducation>) {
    setInstitution(educationObject.institution || '');
    setDegree(educationObject.degree || '');
    setCity(educationObject.location || '');
    setCurrent(!educationObject.dateEnd);
    setStartDate(dayjs(educationObject.dateStart).toISOString());
    educationObject.dateEnd && setEndDate(dayjs(educationObject.dateEnd).toISOString());
    if (educationObject.photo) {
      downloadAvatar(educationObject.photo.location)
    } else {
      setAvatar({})
    }
  }

  async function downloadAvatar(src = '') {
    const response = await fetch(getSizedImageName(src, 'original'), {
      credentials: "include"
    })
    const responseData = await response.arrayBuffer();
    const photoData = await arrayBufferToBase64(responseData);
    const extension = /(?:\.)(\w+)$/i.exec(src)?.[1]
    const photo = 'data:image/' + extension + ";base64," + photoData
    setAvatar({base64: photo, name: src})
  }

  useEffect( () => {
      if (education) setForm(education)
  }, [education])

  const handleImageSubmit = useCallback(images => {
      setAvatar({
        base64: images[0].base64, 
        name: images[0].path
      })}, [setAvatar])

  const handleSubmit = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();

    const newEducation ={
      _id: educationId,
      institution,
      photo: {
        data: avatar?.base64 && base64toBlob(avatar.base64),
        name: avatar?.name
      },
      location: city,
      degree,
      dateStart: startDate,
      dateEnd: endDate
    }

    const data = await addEducation.post(newEducation, personId);
    if (data?.success) {
      setForm({})
      setFormId('none');
    }

    
  };

  const handleCancel = useCallback(() => setFormId('none'), [setFormId])

  const handleDelete = useCallback(async () => {
    const data = await removeEducation.post(educationId, personId);
    if (data?.success) {
      setFormId('none');
    }
  }, [educationId, setFormId, personId, dispatch]);

  useEffect(() => {
    setIsButtonDisable(
      !(
        institution &&
        degree &&
        city &&
        !isNaN(Date.parse(startDate)) &&
        (current || !isNaN(Date.parse(endDate)))
      )
    );
  }, [institution, degree, city, current, startDate, endDate]);

  const handleStartDateChange = useCallback((date: string) => setStartDate(date), []);
  const handleEndDateChange = useCallback((date: string) => setEndDate(date), []);

  return (
    <Form>
      <Row>
        <Col xs={3} className="text-lg-right">
          <Label className="mb-0">
            Avatar
          </Label>
        </Col>
        <Col xs={9}>
          <FalconDropzone
            placeholder={ 
              avatar?.base64 && 
              <div className="avatar avatar-3xl" >
                <img src={avatar?.base64} className="rounded-circle " />
              </div> 
            }
            placeholderText={avatar?.base64 ? "" : "Upload Institution Photo"}
            multiple={false}
            accept="image/*"
            onChange={ handleImageSubmit }
          />
        </Col>
      </Row>

      <AsideFormInput
        id={`institution-${educationId}`}
        label="Institution"
        value={institution}
        onChange={({ target }) => setInstitution(target.value)}
        className="mt-3"
      />

      <AsideFormInput
        id={`degree-${educationId}`}
        label="Degree"
        value={degree}
        onChange={({ target }) => setDegree(target.value)}
      />

      <AsideFormInput id={`city-${educationId}`} label="City" value={city} onChange={({ target }) => setCity(target.value)} />

      <FormGroup>
        <Row className="row">
          <Col lg={{ size: 9, offset: 3 }}>
            <CustomInput
              type="checkbox"
              id={`current-${educationId}`}
              label="I currently study here"
              checked={current}
              onChange={({ target }) => setCurrent(target.checked)}
              inline
            />
          </Col>
        </Row>
      </FormGroup>

      <AsideFormDate
        id={`educationFrom-${educationId}`}
        label="From"
        timeFormat={false}
        dateFormat="MM/DD/YYYY"
        value={startDate}
        onChange={handleStartDateChange}
      />

      {!current && (
        <AsideFormDate
          id={`educationTo-${educationId}`}
          label="To"
          timeFormat={false}
          dateFormat="MM/DD/YYYY"
          value={endDate}
          onChange={handleEndDateChange}
        />
      )}
      {(addEducation.error || removeEducation.error) && 
        <Alert color="danger">{addEducation.error || removeEducation.error}</Alert>
      }
      <FormGroup className="form-group">
        <Row className="row">
          <Col lg={{ size: 3}}>
            {showDelete && 
              <Button color="danger" onClick={handleDelete}>
                <FontAwesomeIcon icon='trash-alt' />
              </Button>
            }

          </Col>
          <Col lg={{ size: 3, offset: 3 }}>
            <Button color="primary" disabled={isButtonDisable} onClick={handleSubmit}>
              <FontAwesomeIcon icon='check' />
            </Button>
          </Col>
          <Col lg={{ size: 3 }}>
          <Button color="secondary" onClick={handleCancel}>
              <FontAwesomeIcon icon='times' />
            </Button>
          </Col>
        </Row>
      </FormGroup>
    </Form>
  );
};


export default EducationForm;
