////// Packages //////////////////
import dayjs from 'dayjs';
import React, { FC, useEffect, useState } from 'react'
import { Input, InputProps } from 'reactstrap'
import { DateTimePickerProps } from './DateTimePicker';
import HappinDateTime from './HappinDateTime'

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

export interface DateTimeInputProps extends Omit<InputProps, 'onChange'> {
    onChange?: (date: string)=>void;
    pickerProps?: Omit<DateTimePickerProps, 'setter' | 'value'>;
    autoFillBlank?: boolean;
}

const DateTimeInput: FC<DateTimeInputProps> = ({dateFormat="MM/DD/YY", timeFormat="hh:mm A", value, onChange, onBlur, onFocus, pickerProps, autoFillBlank, ...inputProps}) => {
    const [dateValue, setDateValue] = useState('');
    const [isOpen, setIsOpen] = useState(false);
    const [firstRender, setFirstRender] = useState(true)
    const format = (dateFormat && timeFormat) ? (dateFormat + ' ' + timeFormat) : ( dateFormat || timeFormat )

    useEffect(()=> {
        if (firstRender) {
            setFirstRender(false);
            if (!value && !autoFillBlank) return;
        }
        
        const d = dayjs(value)
        if ( d.isValid() ) {
            setDateValue( d.format( format ) )
        } else {
            setDateValue( value )
        }
        
    }, [value])

    function handleInputBlur() {
        const d = dayjs(dateValue);
        if ( d.isValid() ) {
            if (onChange) onChange( d.format( format ) )
        } else {
            if (onChange) onChange( dateValue )
        }
    }
    return (
        <>
            <Input 
                onChange={e=>setDateValue(e.target.value)} 
                onBlur={ handleInputBlur } 
                onFocus={ ()=>{
                    setIsOpen(true);

                }}
                value={dateValue}
                {...inputProps} 
            />
            <HappinDateTime 
                onChange={date => {
                    const d = dayjs(date)
                    if ( d.isValid() ) {
                        setDateValue( d.format(format) )
                        if (onChange) onChange( d.format(format) )
                    }
                }} 
                open={isOpen}
                value={dateValue}
                dateFormat={dateFormat}
                timeFormat={timeFormat}
                closeOnClickOutside={true}
                onClose={()=>setIsOpen(false)}
                {...pickerProps}
            />
        </>
    )
}

export default DateTimeInput