////// Packages //////////////////
import React, {FC} from 'react';
import { Card, CardBody } from 'reactstrap';

////// Local Components //////////
import DisplayEmails from './DisplayEmails';
import DisplayAddresses from './DisplayAddresses'; 
import DisplayPhones from './DisplayPhones';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';

interface Props {
    person: MPerson;
    className: string;
}

const Contact: FC<Props> = ({person, className}) => {

    return (
        <Card className={className}>
            <FalconCardHeader title="Contact" light={true} />
            <CardBody >
                <DisplayPhones person={person} className="" />
                <DisplayEmails person={person} className="mt-3" />
                <DisplayAddresses person={person} className="mt-3" />
            </CardBody>
        </Card>
    );
};




export default Contact;