////// Packages //////////////////
import React from 'react';
import { CardBody, Row } from 'reactstrap';

////// Local Components //////////
import ProfileBannerHighlights from '../ProfileBannerHighlights';
import ProfileBannerIntro from '../ProfileBannerIntro';

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const ProfileBannerBody = props => {
  return (
    <CardBody>
      <Row>
        <ProfileBannerIntro {...props} />
        <ProfileBannerHighlights {...props} />
      </Row>
    </CardBody>
  );
};

export default ProfileBannerBody;
