////// Packages //////////////////
import React, { Fragment, useState, FC } from 'react';
import classNames from 'classnames';
import { Card, CardBody, CardProps, Collapse } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import EducationSummary from './EducationSummary';
import EducationForm from './EducationForm';

////// Global Components /////////
import FalconCardHeader from 'components/FalconCardHeader';
import Flex from 'components/Flex';

////// CommonJS Helpers //////////
import { isIterableArray } from 'helpers/utils';

////// Custom Hooks //////////////

////// Thunks ////////////////////

///// Types /////////////////////
import { MPerson } from 'store/types/models/id';

interface Props extends CardProps {
  person: MPerson;
  isEditable: boolean;
}

const Education: FC<Props> = ({ person , isEditable=false, ...rest }) => {
  // Data
  const [formId, setFormId] = useState('none')

  return (
    <Card {...rest}>
      <FalconCardHeader title="Education" light={!isEditable} />
      <CardBody className={classNames('fs--1', { 'bg-light': isEditable })}>
        {isIterableArray(person?.educations) &&
          person.educations?.slice(0).sort((a, b) => a.dateStart < b.dateStart ? 1 : -1).map((education, index, arr) => (
            <EducationSummary 
              {...education} 
              personId={person?._id} 
              setFormId={setFormId} 
              formId={formId} 
              isEditable={isEditable} 
              divider={true} 
              key={index}  
            />
          ))
        }
        {isEditable && (
          <Fragment>
            <Collapse toggler="#togglerAddEducation" isOpen={!formId}>
              <EducationForm 
                personId={person?._id}
                educationId={formId}
                setFormId={setFormId}
               />
              <hr className="border-dashed border-bottom-0 my-4" />
            </Collapse>
            <Collapse isOpen={formId === 'none'}>
              <Flex
                align="center"
                className="mb-4 text-primary cursor-pointer fs-0"
                id="togglerAddEducation"
                onClick={() => {
                  setFormId('');
                }}
              >
                <span className="circle-dashed">
                  <FontAwesomeIcon icon="plus" />
                </span>
                <span className="ml-3">Add new education</span>
              </Flex>
            </Collapse>
          </Fragment>
        )}
      </CardBody>
    </Card>
  );
};


export default Education;
