////// Packages //////////////////
import React, { useState, FC } from 'react';
import { Input, Alert } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import EditableSection from 'compositions/modal/EditableSection';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendOrgTagline } from 'store/thunks/organizations';

interface Props {
    tagline: string;
    organizationId: string;
}

const ModalTagline: FC<Props> = ({tagline, organizationId}) => {
    const setTagline = useAction(sendOrgTagline);

    const [newTagline, setNewTagline] = useState(tagline);

    const handleSubmitTagline = async () => {
        const data = await setTagline.post(newTagline, organizationId)
        return data?.success;
    }

    return (
        <h5 className="mb-3">
            <i>
                <EditableSection
                    baseContent={`"${tagline}"`}
                    editContent={
                        <>
                            <Input
                                type="textarea"
                                rows={2}
                                value={newTagline}
                                onChange={e => setNewTagline(e.target.value)}
                            />
                            {setTagline.error && 
                                <Alert color='danger'>{setTagline.error}</Alert>
                            }
                        </>
                    }
                    handleSubmit={handleSubmitTagline}
                    loading={setTagline.loading}
                />
            </i>
        </h5>
    );
};


export default ModalTagline;
