////// Packages //////////////////
import React, {FC} from 'react'
import { CardHeader, Input, Label, Alert, Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { toast } from 'react-toastify';

////// Local Components //////////

////// Global Components /////////
import ProgressiveImage from 'components/ProgressiveImage';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import usePerson from 'store/hooks/person/usePerson';
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { sendPersonAvatar, sendPersonCover } from 'store/thunks/people';

interface Props {
  personId: string;
  isEditable: boolean;
}

const ProfileBannerHeader: FC<Props> = ({ personId, isEditable=false }) => {
  const newAvatar = useAction(sendPersonAvatar, 15000);
  const newCover = useAction(sendPersonCover, 15000);

  // const avatarUpload = useFileUpload('avatar');
  // const coverUpload = useFileUpload('cover');

  const {value: person} = usePerson(personId);

  const handleCoverPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (!event.target.files) return;

    const data = await newCover.post(event.target.files[0], personId)
    if (!data?.success) {
      toast('Failed to upload. Please try again')
    }
  }

  const handleAvatarPhoto = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (!event.target.files) return;

    const data = await newAvatar.post(event.target.files[0], personId)
    if (!data?.success) {
      toast('Failed to upload. Please try again')
    }

  }

  return (
    <CardHeader className={`position-relative min-vh-25 ${isEditable ? 'mb-8' : 'mb-7'}`}>
      <div className="cover-image">
        <div className="bg-holder overflow-hidden rounded-soft rounded-bottom-0 bg-dark position-relative">
            {(newCover.loading || newCover.error) && 
              <div className="position-absolute h-100 w-100 d-flex justify-content-center align-items-center" style={{backgroundColor: newCover.error ? "rgba(100, 0, 0, 0.4)" : "rgba(0, 0, 0, 0.4)"}}>
                { newCover.loading && <Spinner color="primary" size="l" /> }
                { newCover.error && <Alert color="danger">{newCover.error}</Alert> }
              </div>
            }
            <ProgressiveImage src={person?.coverPhoto?.location || ''} width="100%" height={250} size='l' style={{ objectFit: 'cover' }} />
        </div>
        {isEditable && 
        <>
          <Input 
            className="d-none" 
            id="upload-cover-image" 
            type="file"
            accept=".jpg,.jpeg,.png,.heic,.tiff" 
            onChange={handleCoverPhoto}
          />
          <Label className="cover-image-file-input" htmlFor="upload-cover-image">
            <FontAwesomeIcon icon="camera" className="mr-2" />
            <span>Change cover photo</span>
          </Label>
        </>
        }
      </div>
      <div className="avatar avatar-5xl avatar-profile shadow-sm img-thumbnail rounded-circle">
        <div className="h-100 w-100 rounded-circle overflow-hidden position-relative">
          {(newAvatar.loading || newAvatar.error) && 
            <div className="position-absolute h-100 w-100 d-flex justify-content-center align-items-center" style={{backgroundColor: newAvatar.error ? "rgba(100, 0, 0, 0.4)" : "rgba(0, 0, 0, 0.4)"}}>
              { newAvatar.loading && <Spinner color="primary" size="l" /> }
              { newAvatar.error && <Alert color="danger">{newAvatar.error}</Alert>}
            </div>
          }
          <ProgressiveImage src={person?.avatar?.location || ''} size="l" width="200" />
          {isEditable && 
          <>
            <Input
              className="d-none"
              id="profile-image"
              type="file"
              accept=".jpg,.jpeg,.png,.heic,.tiff"
              onChange={handleAvatarPhoto}
            />
            <Label className="mb-0 overlay-icon d-flex flex-center" htmlFor="profile-image">
              <span className="bg-holder overlay overlay-0" />
              <span className="z-index-1 text-white text-center fs--1">
                <FontAwesomeIcon icon="camera" />
                <span className="d-block">Update</span>
              </span>
            </Label>
          </>
          }
        </div>
      </div>
    </CardHeader >
  );
};


export default ProfileBannerHeader;
