import React, { FC } from 'react';
import { Col, FormGroup, Label, Row } from 'reactstrap';
import DateTimeInput, {DateTimeInputProps} from 'components/datetime/DateTimeInput';

interface Props extends DateTimeInputProps {
  id: string;
  label: string;
}

const AsideFormDate: FC<Props> = ({ id, label, className, timeFormat=false, dateFormat="MM/DD/YYYY", pickerProps, ...rest }) => {
  return (
    <FormGroup className={`form-group ${className}`}>
      <Row>
        <Col lg={3} className="text-lg-right">
          <Label className="mb-0" htmlFor={id}>
            {label}
          </Label>
        </Col>
        <Col lg={9}>
            <DateTimeInput
                id={id}
                timeFormat={timeFormat}
                dateFormat={dateFormat}
                pickerProps={{
                    closeOnSelect: true,
                    ...pickerProps
                }}
                {...rest}
            />
        </Col>
      </Row>
    </FormGroup>
  )
};

export default AsideFormDate;
