////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////

////// Global Components /////////
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia';
import { MOrganization } from 'store/types/models/id';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
    organization: MOrganization;
    className: string;
}

const DisplayPhones: FC<Props> = ({organization, className}) => {
    return (
        <>
            {organization?.phones && organization.phones.length > 0 && 
                <AsideResponsiveMedia icon="phone" className={className}>
                    <h6 className="mb-0 font-weight-bold">Phone</h6>
                    {organization?.phones?.map( ({description, number}, index) => (
                        <h6 key={index} className="mb-0 pt-1 pt-sm-0">
                            <em className="pr-1 d-block d-sm-inline">
                                {description}:
                            </em>
                            {number} 
                        </h6>
                    ))}
                </AsideResponsiveMedia>
            }
        </>
    );
};


export default DisplayPhones;
