import { shallowEqual } from 'react-redux';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect';
import { getPersonPhotos } from 'store/thunks/people';
import { MPersonPhoto } from 'store/types/models/id';

const usePersonPhotos = (personId: string, delimiter?: string) => {
    const photos = useLoadAndSelect<MPersonPhoto[]>({
        actionCreator: getPersonPhotos,
        selector(state) { 
            return Object.values(state.id.personPhotos).filter( photo => photo.personId === personId)
        },
        equality: shallowEqual,
        postParams: [personId, delimiter],
        deduplicate: ['getPersonPhotos', personId]
    })

    return photos;
}

export default usePersonPhotos;