import React, { FC } from 'react';
import { Col, Button } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

interface Props {
  isEditable?: boolean;
  setOpenModal: (arg: boolean)=>void;
}

const ProfileBannerHighlights: FC<Props> = ({ isEditable=false, setOpenModal }) => {

  return (
    <Col xs="auto" className="pl-2 ml-lg-auto pl-lg-3 d-flex">
        <div className="mt-auto">
            {isEditable && 
                <Button color="primary" size="sm" onClick={() => setOpenModal(true)} >
                  <FontAwesomeIcon icon="pencil-alt" className="mr-2" />
                  Edit Profile 
                </Button>
            }
            
            
        </div>
    </Col>
  );
};



export default ProfileBannerHighlights;
