////// Packages //////////////////
import React, {FC} from 'react';
import { Col, Row } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import Avatar from 'components/Avatar';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import {IPhoto} from 'store/types/models/id';
import { IconProp } from '@fortawesome/fontawesome-svg-core';


interface Props {
    icon: IconProp;
    avatar?: IPhoto;
    className?: string;
    children: React.ReactNode;
    name?: string;
}

const AsideResponsiveMedia: FC<Props> = ({icon, avatar, className, children, name}) => {
    return (
        <Row className={className}>
            <Col xs="12" sm="1" lg="2"  className="pb-2 d-flex justify-content-center pr-sm-0 d-sm-block">
                {icon && <FontAwesomeIcon icon={icon} className="text-700 fs-2 mr-2" />}
                {!icon && avatar && <Avatar size="xl" src={avatar.location} name={name} />}
            </Col>
            <Col xs="12" sm="11" lg="10" className="d-flex flex-column text-center d-sm-block text-sm-left pl-sm-0 ">
                {children}
            </Col>
        </Row>
    );
};


export default AsideResponsiveMedia;
