/////// Packages /////////////////
import React, { useRef, useState } from 'react';
import {
  Media,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';
import confirm from 'reactstrap-confirm';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { toast } from 'react-toastify';
import { useSelector } from 'react-redux';

////// Local Components //////////
import AddPersonModal from './AddPersonModal';

////// Global Components /////////
import Flex from 'components/Flex';
import Avatar from 'components/Avatar';
import PaginatingTableCard from 'compositions/table/PaginatingTableCard';

////// CommonJS Helpers //////////
import makeName from 'helpers/makeName';

////// Custom Hooks //////////////
import useContactsList from 'store/hooks/person/useContactsList';
import useAction from 'store/hooks/useAction';
import useTypingTimer from 'hooks/useTypingTimer';

////// Thunks ////////////////////
import { removePersonContact } from 'store/thunks/people';

////// Types /////////////////////
import { $BasicPerson, MPerson } from 'store/types/models/id';


// import customers from '../../data/e-commerce/customers';

type NameFormatterProps = Partial<MPerson> & {organizationName: string};

const nameFormatter = (_dataField: string, { _id, avatar, name }: NameFormatterProps) => {
  const displayName = makeName(name)
  return (
    <Link to={`/social/profile/${_id}`}>
      <Media tag={Flex} align="center">
        <Avatar src={avatar?.location} name={displayName} />
        <Media body className="ml-2">
          <h5 className="mb-0 fs--1">{displayName}</h5>
        </Media>
      </Media>
    </Link>
  );
};

const organizationFormatter = (organizationName: string, { organizationId }: NameFormatterProps) => organizationId ? <Link to={`/social/organizations/${organizationId}`}>{organizationName}</Link> : <></>;

const phoneFormatter = (number?: string) => number ? <a href={`tel:${number}`}>{number}</a> : <></>;

const emailFormatter = (email?: string) => email ? <a href={`mailto:${email}`}>{email}</a> : <></>;

const ContactPeople = () => {
    const [openModal, setOpenModal] = useState(false);
    const loginOrganization = useSelector( state => state.app.login.organizationId )
    // const [openRemoveModal, setRemoveModal] = useState(false);
    // const [personToRemove, setPersonToRemove] = useState('');

    const removePerson = useAction( removePersonContact )
    const timer = useTypingTimer(750)
    const [searchText, setSearchText] = useState('');
    
    const { value: contacts, ...contactsLoader } = useContactsList( searchText );

    const csvExportRef = useRef(undefined as {onExport: ()=>void} | undefined);
    // const handleClose = useCallback( () => setRemoveModal(false), [ setRemoveModal ] );
    // const toggle = useCallback( () => setRemoveModal( !openRemoveModal ), [ openRemoveModal, setRemoveModal ] );

    // const handleRemovePerson = useCallback(async () => {
    //     if (personToRemove) {
    //     const data = await removeUser.post(personToRemove);
    //     if (data?.success) {
    //         setPersonToRemove('');
    //         toast.success('Successfully removed user');
    //         setRemoveModal(false);
    //     }
    //     }
    // }, [personToRemove])

    

    // const actionFormatter = (dataField: string) => {
    
    // // Control your row with this id
    // return (
    //     <UncontrolledDropdown>
    //     <DropdownToggle color="link" size="sm" className="text-600 btn-reveal mr-3">
    //         <FontAwesomeIcon icon="ellipsis-h" className="fs--1" />
    //     </DropdownToggle>
    //     <DropdownMenu right className="border py-2">
    //         <DropdownItem onClick={ () => {
    //             setPersonToRemove(dataField)
    //             setRemoveModal(true) 
    //         } } className="text-danger">
    //         Remove User
    //         </DropdownItem>
    //     </DropdownMenu>
    //     </UncontrolledDropdown>
    // )
    // };
    const contactTableInfo = contacts.map( c => ({
        ...c,
        phone: c.phones?.[0],
        email: c.emails?.[0]
    }))

    async function handleRemovePerson(_id: string) {
        const confirmed = await confirm({
            title: 'Remove Contact?',
            confirmText: 'Remove',
            confirmColor: 'danger',
            cancelText: "Cancel",
            cancelColor: 'secondary'
        })
        if (!confirmed) return;
        const data = await removePerson.post(_id);
        if (data?.success) {
            toast.success('Successfully deleted person')
        } else {
            toast.error(data?.error || 'Something went wrong')
        }
    }

    function contactActionsFormatter(_id: string, person: $BasicPerson) {
        return (
            <UncontrolledDropdown>
            <DropdownToggle color="link" size="sm" className="text-600 btn-reveal mr-3">
                <FontAwesomeIcon icon="ellipsis-h" className="fs--1" />
            </DropdownToggle>
            <DropdownMenu right className="border py-2">
                { person.organizationId !== loginOrganization && 
                    <DropdownItem onClick={() => handleRemovePerson(_id)} className="text-danger">
                        Delete Contact
                    </DropdownItem>
                }
            </DropdownMenu>
            </UncontrolledDropdown>
        )
    }

    const columns = [
        {
            dataField: 'name.last',
            text: 'Name',
            headerClasses: 'border-0',
            classes: 'border-0 py-2 align-middle',
            formatter: nameFormatter,
            csvFormatter: (_: string, person: MPerson) => makeName(person.name, {first: true, middle: true, last: true, prefix: true, title: true, reverse: true}),
            sort: true,
            search: (data: MPerson) => (['prefix', 'first', 'common', 'middle', 'last', 'title'] as const).map(t => data.name?.[t]).filter(s=>s).join(' '),
        },
        {
            dataField: 'organizationName',
            headerClasses: 'border-0 d-none d-md-table-cell',
            text: 'Organization',
            classes: 'border-0 py-2 align-middle d-none d-md-table-cell',
            formatter: organizationFormatter,
            sort: true,
            search: true
        },
        {
            dataField: 'position',
            headerClasses: 'border-0 d-none d-lg-table-cell',
            text: 'Position',
            classes: 'border-0 py-2 align-middle d-none d-lg-table-cell',
            sort: true,
            search: true
        },
        {
            dataField: 'email.address',
            headerClasses: 'border-0 d-none d-lg-table-cell',
            text: 'Email',
            classes: 'border-0 py-2 align-middle d-none d-lg-table-cell',
            formatter: emailFormatter,
            sort: true,
            search: true
        },
        {
            dataField: 'phone.number',
            headerClasses: 'border-0',
            text: 'Phone',
            classes: 'border-0 py-2 align-middle',
            formatter: phoneFormatter,
            sort: true
        },
        {
            dataField: '_id',
            headerClasses: 'border-0',
            text: 'Actions',
            classes: 'border-0 py-2 align-middle',
            formatter: contactActionsFormatter
        }
        ];

    return (
        <>
            <PaginatingTableCard
                title="People"
                cardClass="mb-3"
                paginationOptions={{
                    sizePerPage: 12
                }}
                csvExportRef={csvExportRef}
                exportCSV={{fileName: "Happin Organizations Export " + dayjs().format("MM-DD-YYYY") + '.csv'}}
                data={contactTableInfo || []}
                columns={columns}
                handleNew={()=>setOpenModal(true)}
                onSearchText={text=>timer(()=>setSearchText(text))}
                search
            />
            <AddPersonModal setIsOpen={setOpenModal} isOpen={openModal} />
        </>
    );
}


export default ContactPeople;