////// Packages //////////////////
import React, {FC} from 'react';

////// Local Components //////////

////// Global Components /////////
import AsideResponsiveMedia from 'compositions/AsideResponsiveMedia';
import {MOrganization} from 'store/types/models/id';
////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
    organization: MOrganization;
    className: string;
}

const DisplayAddresses: FC<Props> = ({organization, className}) => {
    return (
        <>
            {organization?.addresses && organization.addresses.length > 0 &&
                    <AsideResponsiveMedia icon="home" className={className}>
                            <h6 className="mb-0 font-weight-bold">Addresses</h6>
                            {organization?.addresses?.map( ({title, street, city, state, zipCode}, index) => ( 
                                <h6 key={index} className="mb-0 pt-1 pt-sm-0">
                                    <em className="pr-1">
                                        {title}:
                                    </em> 
                                    <br />
                                    {street.map( (streetAddressLine, indx) => (
                                        <h6 key={indx} className="mb-0 ml-2">
                                            {streetAddressLine}
                                        </h6>
                                    ))}
                                    <h6 className="mb-0 ml-2">
                                        {city}, {state} {zipCode} 
                                    </h6>
                                </h6>
                            ))}
                    </AsideResponsiveMedia>
                }
        </>
    );
};


DisplayAddresses.propTypes = {

};


export default DisplayAddresses;
