import { isEqual } from 'lodash';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect';
import { getSetPeopleById } from 'store/thunks/people';
import { MPerson } from 'store/types/models/id';

const usePerson = (personId: string | undefined) => {
    const person = useLoadAndSelect<MPerson>({
        actionCreator: getSetPeopleById,
        selector(state) { 
            return state.id.people[personId || '']
        },
        equality: isEqual,
        postParams: personId,
        deduplicate: ['personInfo', personId || '']
    })

    return person;
}

export default usePerson;